
        @import "src/styles/scss/common.scss";
        
.page {
  display: flex;
  flex-direction: column;
  align-items: stretch;
  height: 100%;
  position: relative;

  .header {
    position: relative;
    color: $white;
    font-size: 12px;
  }

  .logo {
    height: 34px;
    width: 100px;
    background-repeat: no-repeat;
    background-size: 100%;
    background-image: url("~@/assets/images/payment/logo.png");
  }

  .bg {
    height: 140px;
    width: 340px;
    margin: 0 auto;
    background-repeat: no-repeat;
    background-size: 100%;
    background-position: center center;
    background-image: url("~@/assets/images/payment/bg.png");
  }

  .text {
    position: absolute;
    top: 50px;
    left: 60px;
  }

  .wrapper {
    position: absolute;
    top: 80px;
    width: 100%;
    height: 50px;
    background-repeat: no-repeat;
    background-size: 100%;
    background-image: url("~@/assets/images/payment/mask.png");

  }

  .tabs {
    margin: 16px;
    display: flex;
    justify-content: space-around;

    .payment_tab {
      height: 64px;
      width: 64px;
      border-radius: 8px;
      background-color: $white;
      border: 1px solid rgba(226, 241, 222, 1);
      box-sizing: border-box;
      text-align: center;
      display: flex;
      flex-direction: column;
      justify-content: center;
      cursor: pointer;
      
      .payment_text {
        font-size: 10px;
      }

      &.active {
        background-repeat: no-repeat;
        background-size: 100%;
        background-image: url("~@/assets/images/payment/payment-active.png");

        .payment_text {
          color: $primary_color;
          
        }
      }

    }


    .payment_icon {
      height: 32px;
      width: 32px;
      margin: 0 auto;

      &.bank {
        background-repeat: no-repeat;
        background-size: 100%;
        background-image: url("~@/assets/images/payment/bank-icon-color.png");
      }

      &.wechat {
        background-repeat: no-repeat;
        background-size: 100%;
        background-image: url("~@/assets/images/payment/wechat-icon-color.png");
      }

      &.alipay {
        background-repeat: no-repeat;
        background-size: 100%;
        background-image: url("~@/assets/images/payment/alipay-icon-color.png");
      }

      &.USDT {
        background-repeat: no-repeat;
        background-size: 100%;
        background-image: url("~@/assets/images/payment/alipay-icon-color.png");
      }


    }

    .payment_text {
      font-size: 12px;
      line-height: 24px;
    }
  }
}

.count {
  padding: $page_padding;
  box-sizing: border-box;
  font-size: 14px;
  border-bottom: $border_opacity;

  .total {
    color: $font_color_tip;
  }
}

.empty {
  display: flex;
  flex-direction: column;
  justify-content: center;
  color: $font_color_tip;
  font-size: 12px;
  text-align: center;
  align-items: center;
  padding: $page_padding;

  .empty_bg {
    height: 130px;
    width: 120px;
    margin-bottom: 30px;
    background-repeat: no-repeat;
    background-size: 100%;
    background-image: url("~@/assets/images/payment/empty.png");
  }
}

.list {
  :global(.adm-swipe-action) {
    --background: transparent;
    background: transparent;
  }

  :global(.adm-list-body) {
    background-color: transparent;
  }
}

.listItem {
  margin: 10px;
  // border-radius: 10px;
  overflow: hidden;

  &:global(.adm-list-item) {
    padding: 0;
    background-color: transparent
  }

  :global(.adm-list-item-content),
  :global(.adm-list-item-content-main) {
    padding: 0;
    border: none;
  }
}

.submit_icon {
  width: 16px;
  height: 16px;
  display: inline-block;
  vertical-align: middle;
  margin-right: 2px;
  background-repeat: no-repeat;
  background-size: 100%;
  background-image: url("~@/assets/images/payment/icon-jia-white.png");
}