
        @import "src/styles/scss/common.scss";
        
@import "src/styles/scss/common.scss";

.form {
  .tip {
    font-size: 14px;
    color: $font_color_tip;
  }

  :global(.adm-form-item.adm-form-item-vertical .adm-form-item-label) {
    font-size: 14px;
    font-weight: 500;
    line-height: 22px;
    padding: 10px 10px 0;
    color: $font_color;
  }

  :global(.adm-input-element) {
    font-size: 14px;
    line-height: 20px;
    padding: 0 10px;

    color: $primary_color;
    padding: $page_padding;
    background-color: $white;

    &::placeholder {
      color: $font_color_tip;
    }
  }

  :global(.adm-text-area-element) {
    font-size: 14px;
    line-height: 20px;
    padding: $page_padding;
    background-color: $white;
    color: $primary_color;

    &::placeholder {
      color: $font_color_tip;
    }
  }

  :global(.adm-list-body) {
    background-color: transparent;
    border-top: none;
    border-bottom: none;
  }

  :global(.adm-list-item) {
    background-color: transparent;
    padding: 0;
  }


  :global(.adm-list-item-content) {
    border: none;
    padding: 0;
    align-items: center;
  }

  :global(.adm-list-item-content-main) {
    border-bottom: none;
    box-sizing: border-box;
    padding: 0;
  }


}


.submit {
  background: linear-gradient(93.54deg, $submit_btn_bg_from 2.8%, $primary_color 94.95%);
  width: 95%;
  display: block;
  margin: 20px auto;
  color: $white;
  font-size: 14px;
  line-height: 26px;
}

.tip {
  color: $font_color_tip;
  text-align: center;
  margin-top: 20px;
}

.boardBackground {
  background: linear-gradient(to right, #05c157, #0cd259);
  padding: 24px 20px 0;
  position: relative;
}

.content {
  display: flex;
  align-items: center;
}

.avatar {
  display: inline-block;
  width: 40px;
  height: 40px;
  border-radius: 50%;
  margin-right: 5px;
}

.username {
  color: $font_color;
  font-size: 16px;
  font-family: PingFang SC;
  font-style: normal;
  font-weight: 500;
  line-height: normal;
}

.levelWrapper {
  display: flex;
  align-items: center;
}

.levelLabel {
  color: $font_color;
  font-size: 12px;
  font-family: PingFang SC;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
  margin-right: 5px;
}

.orderType {
  box-sizing: border-box;
  width: 73px;
  height: 24px;
  background-color: $primary_color;
  border-top-left-radius: 12px;
  border-bottom-left-radius: 12px;
  position: absolute;
  right: 0;
  top: 16px;
  line-height: 24px;

  color: $white;
  text-align: center;
  font-family: PingFang SC;
  font-size: 14px;
  font-style: normal;
  font-weight: 500;
}

.boardContent {
  padding-top: 46px;
  padding-bottom: 16px;
}

.validAmount {
  color: $black;
  text-align: center;
  font-size: 16px;
  font-family: PingFang SC;
  font-style: normal;
  font-weight: 500;
  line-height: 24px;
}

.amount {
  color: var(--unnamed, $primary_color);
  text-align: center;
  leading-trim: both;
  text-edge: cap;
  font-size: 32px;
  font-family: DINPro;
  font-style: normal;
  font-weight: 900;
  line-height: normal;
}

.amountIcon {
  width: 28px;
  height: 14px;
  vertical-align: baseline;
}