
        @import "src/styles/scss/common.scss";
        
.card {
  box-sizing: border-box;
  display: flex;

  .item_text {
    flex: 1;
    font-weight: 500;
    font-size: 14px;
    line-height: 24px;
    color: $white;
    margin: 0 10px;
  }

  .item_icon {
    height: 32px;
    width: 32px;
    border-radius: 32px;
  }



  .weight {
    font-weight: 500;
  }

}