
        @import "src/styles/scss/common.scss";
        
.pageTrade {
    height: 100%;
    background-repeat: no-repeat;
    background-size: 100%;
    background-image: url("~@/assets/images/mask-bg.png");
    overflow-y: auto;
    :global(.adm-tabs-tab-active) {
        color: $primary_color;
        box-sizing: border-box;
    }

    :global(.adm-capsule-tabs-tab) {
        font-size: 013px;
        border-radius: 8px;
        line-height: 040px;
        box-sizing: border-box;
        padding: 0;
        background-color: transparent;
        border: 1px solid $primary_color;
    }

    :global(.adm-capsule-tabs-content) {

        box-sizing: border-box;
        padding: 0;
    }

    :global(.adm-capsule-tabs-tab-active) {
        background-repeat: no-repeat;
        background-size: 100%;
        background-color: $primary_color;
        border: none;
    }
}

.header {
    height: 44px;
    padding: 0 10px;
    display: flex;
}

.logo {
    height: 44px;
}

// 头部右侧
.headerRight {
    margin-left: auto;
    display: flex;
    align-items: center;
}

// 海报
.poster {
    margin: 0 14px 14px;
    width: 347px;
    height: 136px;
}

.posterWrapper {
    position: relative;
}

.posterButton {
    position: absolute;
    width: 88px;
    height: 32px;
    left: 38px;
    top: 98.5px;
    background: transparent;
    border: none;
}

.listHeader {
    font-size: 12px;
    color: #888888;
    padding: 0 14px;
    display: flex;
    align-items: center;
    position: sticky;
    top: 0;
    background-color: $white;
}

.listSelectedSortRule {
    font-size: 12px;
    line-height: 16px;
    color: $font_color;
}

.listSortRuleItem {
    font-size: 12px;
    color: $font_color;
}

.listFilterButton {
    margin-left: auto;
}

.whitespace {
    height: 120px;
}