
        @import "src/styles/scss/common.scss";
        
.main {}

.boardContent {
    padding-top: 46px;
    padding-bottom: 16px;
}

.boardBackground {
    background: linear-gradient(to right, #05c157, #0cd259);
    padding: 18px 20px 0;
    position: relative;
}


.content {
    display: flex;
    align-items: center;
}

.avatar {
    display: inline-block;
    width: 40px;
    height: 40px;
    border-radius: 50%;
}

.username {
    color: $font_color;
    font-size: 16px;
    font-family: PingFang SC;
    font-style: normal;
    font-weight: 500;
    line-height: normal;
}

.levelWrapper {
    display: flex;
    align-items: center;
}

.levelLabel {
    color: $font_color;
    font-size: 12px;
    font-family: PingFang SC;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
    margin-right: 5px;
}

.boardContent {
    padding-top: 46px;
    padding-bottom: 16px;
}

.validAmount {
    color: $black;
    text-align: center;
    font-size: 16px;
    font-family: PingFang SC;
    font-style: normal;
    font-weight: 500;
    line-height: 24px;
}

.amount {
    color: var(--unnamed, $primary_color);
    text-align: center;
    leading-trim: both;
    text-edge: cap;
    font-size: 32px;
    font-family: DINPro;
    font-style: normal;
    font-weight: 900;
    line-height: normal;
}

.amountIcon {
    width: 28px;
    height: 14px;
    vertical-align: baseline;
}

.selector {
    margin-top: 8px;

    :global(.adm-selector-item) {
        padding: 6px 12px;
        border-radius: 4px;
        border: 1px solid rgba($primary_color, 0.1);
        background: $white;

        color: var(--unnamed, $primary_color);
        text-align: justify;
        font-family: PingFang SC;
        font-size: 14px;
        font-style: normal;
        font-weight: 400;
        line-height: 24px;
        /* 171.429% */
    }

    :global(.adm-selector-item-active) {
        border: 1px solid $primary_color;
    }

    :global(.adm-selector-check-mark-wrapper) {
        border-bottom-color: $primary_color;
        border-right-color: $primary_color;
    }
}

.input {
    margin-top: 8px;
}

.publish {
    display: flex;
    width: 347px;
    height: 44px;
    justify-content: center;
    align-items: center;
    gap: 10px;
    flex-shrink: 0;

    border-radius: 8px;
    background: var(--1, linear-gradient(137deg, $submit_btn_bg_from 0%, $primary_color 100%));

    color: $white;
    text-align: center;
    font-family: PingFang SC;
    font-size: 16px;
    font-style: normal;
    font-weight: 500;
    line-height: normal;

    margin: 8px auto;
}

.txtDanger{
    color: #f70000;
}
.txtAction{
    background: $primary_color;
    color: $white !important;
}

.professionWrap{
    padding: 16px 12px;
    >p{
        display: flex;
        align-items: center;
        justify-content: space-between;
    }
    .subsidyRate,.subsidyAmount{
        font-weight: 600;
        font-size: 12px;
        span{
            font-weight: 500;
        }
    }
    .professionItem{
        display: flex;
        flex-wrap: wrap;
        margin-top: 16px;
        a{
            flex: 0 0 32%; 
            padding: 0px 2px;
            height: 32px; 
            text-align:center;
            line-height:32px;
            font-size: 12px;
            margin-right: calc(4% / 3); 
            margin-bottom: 10px; 
            color: $primary_color;
            border: 1px solid $primary_color;
            border-radius: 5px;
            overflow: hidden;
            text-overflow: ellipsis;
            white-space: nowrap;
        }
        a:nth-child(3n){ 
            margin-right: 0; 
        } 
          /* 使最后一个元素的边距填满剩余空间 */
        a:last-child{ 
            margin-right: auto; 
        } 
    }
}


.tips{
    margin-top: 20px;
    padding: 0px 15px 22px 15px;
    line-height: 18px;
    h4{
        font-size: 14px;
        font-weight: 600;
        margin-bottom: 3px;
    }
    p{
        font-size: 12px;
    }
}

.headerAvatar{
    padding: 9px;
    position: relative;
}


.tabsHeader{
    display: flex;
    padding:0 10px;
    font-size: 12px;
    margin-bottom: 10px;
}
.tabs {
    :global(.adm-tabs-tab-active){
        color: $primary_color;
    }
    :global(.adm-tabs-tab-line) {
      background: $primary_color;
    }
    :global(.adm-tabs-header) {
      border: none
    }
}
