$positive: $primary_color;
$negative: #fb2222;
$normal: #FF9900;

$normal-list: (
    WaitingSellerConfirm,
    WaitingBuyerPay,
    BuyerConfirmsOrderHasBeenPaid
);

$positive-list: (
    AdminCompletesOrder,
    SellerCompletesOrder
);

$negative-list: (
    BuyerNotAppeals,
    SellerCancelsPurchase,
    BuyerNotPay,
    SellerHasNotReceivedPayment,
    BuyerAppeals,
    AdminCancelsOrder,
    BuyerPaymentExceedsTimeLimit
);

@each $el in $normal-list {
    .order-status-#{$el} {
        color: $normal !important;
    }
}

@each $el in $positive-list {
    .order-status-#{$el} {
        color: $positive !important;
    }
}

@each $el in $negative-list {
    .order-status-#{$el} {
        color: $negative !important;
    }
}