
        @import "src/styles/scss/common.scss";
        
.balance_tabs {
    :global(.adm-tabs-tab) {
        font-size: 16px;
        height: 32px;
        line-height: 20px;
        box-sizing: border-box;
        background-color: transparent;
        padding: 0 10px;
    }

    :global(.adm-tabs-tab-active) {
        color: $primary_color;

        box-sizing: border-box;

    }

    :global(.adm-tabs-tab-line) {
        background: $primary_color;

    }

    :global(.adm-tabs-header) {
        border: none
    }

    :global(.adm-tabs-content) {
        padding: 10px 10px 0;
    }
}


.exchangeActions {
    height: 110px;
    background-repeat: no-repeat;
    background-size: 100%;
    background-image: url("~@/assets/icons/home/card.png");
    overflow: hidden;
    box-sizing: border-box;
    cursor: pointer;
    position: relative;
    padding: 16px;
    // box-shadow: 0px 4px 12px 0px rgba(70, 228, 86, 0.37);

    .exchangeActionItem {
        text-align: center;
        display: flex;
        flex-direction: column;
        justify-content: center;

        .exchangeActionIcon {
            width: 100%;
        }

        .exchangeActionText {
            color: $white;
            margin-top: 8px;
        }
    }
}

.funcActions {
    padding: 4px 16px 10px;
    box-sizing: border-box;
    border-top: $border_opacity;

    .funcActionsItem {
        cursor: pointer;
        text-align: center;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;

        .funcActionIcon {
            margin-top: 10px;
            width: 56px;
            height: 56px;
            background-color: $white;
            border-radius: 12px;
            box-sizing: border-box;
            display: flex;
            align-items: center;
            justify-content: center;
            box-shadow: 0px 4px 8px rgba(217, 240, 233, 0.5);
        }

        .funcActionText {
            margin-top: 10px;
            font-size: 12px;
        }
    }
}

.order_list {
    height: 410px;
    box-shadow: 0px -16px 16px rgba(13, 57, 33, 0.1);
    border-radius: 20px 20px 0 0;
    cursor: pointer;


    :global(.adm-capsule-tabs-tab) {
        font-size: 13px;
        border-radius: 8px;
        line-height: 40px;
        box-sizing: border-box;
        padding: 0;
        background-color: transparent;
        border: 1px solid $primary_color;
    }

    :global(.adm-capsule-tabs-content) {

        box-sizing: border-box;
        padding: 0;
    }

    :global(.adm-capsule-tabs-tab-active) {
        background: linear-gradient(93.54deg, $submit_btn_bg_from 2.8%, $primary_color 94.95%);
        background-repeat: no-repeat;
        background-size: 100%;
        border: none;

        .order_list_action_left {
            background-image: url("~@/assets/icons/home/order-button-left.png");
        }

        .order_list_action_right {
            background-image: url("~@/assets/icons/home/order-button-right.png");
        }

    }


}

.notice {
    padding:0 10px 10px;
    cursor: pointer;
    width: 100%;
    overflow: hidden;
    box-sizing: border-box;
    line-height: 20px;

    :global(.adm-space-item) {
        &:last-of-type {
            flex: 1;
            overflow: hidden;
        }
    }

    :global(.adm-notice-bar) {
        background-color: transparent;
        color: #333;
        border-color: transparent;
    }
}

.content {
    margin-top: 10px;

    .space {
        flex: 1;
        background-color: $white;
    }

    .header {
        padding: $page_padding;
    }
}

.whitespace {
    height: 120px;
}

.iframe {
    width: 100%;
    height: 45vh;
}

.swiperBox{
    padding: 0px 10px;
    .swiperWrap{
        max-height: 300px;
        overflow: auto;
    }
}
.btnColor{
    color: $primary_color;
}

.tabs{
    margin-bottom: 14px;
    :global(.adm-tabs-tab-active) {
      color: $primary_color;
      box-sizing: border-box;
    }
    :global(.adm-tabs-tab-line) {
      color: $primary_color;
      background-color: $primary_color;
    }
    :global(.adm-tabs-tab){
        font-weight: 600;
        padding: 14px 0px;
    }
  
    :global(.adm-capsule-tabs-tab) {
        font-size: 013px;
        border-radius: 8px;
        line-height: 040px;
        box-sizing: border-box;
        padding: 0;
        background-color: transparent;
        border: 1px solid $primary_color;
    }
  
    :global(.adm-capsule-tabs-content) {
  
        box-sizing: border-box;
        padding: 0;
    }
  
    :global(.adm-capsule-tabs-tab-active) {
        background-repeat: no-repeat;
        background-size: 100%;
        background-color: $primary_color;
        border: none;
    }
  }