
        @import "src/styles/scss/common.scss";
        
.wrapper {
  height: 100%;
  max-height: 100%;
  display: flex;
  flex-direction: column;
  align-items: stretch;
}

.tabs {
  :global(.adm-tabs-tab) {
    font-size: 16px;
    // height: 32px;
    line-height: 22px;
    box-sizing: border-box;
    padding: 0;
    background-color: transparent;
    padding: 10px 0;

  }

  :global(.adm-tabs-tab-list) {
    border-bottom: $border_opacity;

  }

  :global(.adm-tabs-content) {
    padding: 20px 0 0;
    box-sizing: border-box;
  }

  :global(.adm-tabs-tab-active) {
    color: $primary_color;

    box-sizing: border-box;

  }

  :global(.adm-tabs-tab-line) {
    background: $primary_color;

  }

  :global(.adm-tabs-header) {
    border: none
  }
}

.content {
  padding-top: 10px;
  flex: 1;
  overflow-y: auto;

  background-color: $sub_page_bg_color;

  .table {
    th {
      font-size: 14px;
      padding: 6px 10px;
      color: $font_color;
      background-color: $primary_color_opacity;
      border-right: $border_opacity;
      line-height: 20px;
    }

    td {
      font-size: 12px;
      padding: 6px 10px;
      color: $font_color_sub;
      border-right: $border_opacity;
      border-bottom: $border_opacity;
      line-height: 20px;
    }

    .bold {
      font-weight: 600;
    }
  }
}



.footer {
  padding: 10px 10px;
  font-weight: 600;
  color: $font_color_sub;
}