
        @import "src/styles/scss/common.scss";
        
.list {

  border-top: none;
  border-bottom: none;

  :global(.adm-list-body) {
    background-color: transparent;
    border-top: none;
    border-bottom: none;
  }

  :global(.adm-list-item) {
    background-color: transparent;
    padding: 0;
    border-bottom: $border_opacity;
  }


  :global(.adm-list-item-content) {
    border: none;
    padding: 0;
    align-items: center;
  }

  :global(.adm-list-item-content-main) {
    border-bottom: none;
    box-sizing: border-box;
    padding: 10px;
  }

  .item {
    display: flex;
    align-items: center;

    .label {
      font-size: 16px;
      color: $font_color;
    }

    .value {
      color: $font_color_tip;
      font-size: 14px;

    }

    .space {
      flex: 1;
    }

    .icon {
      margin-left: 10px;
    }

    .switch {
      // --checked-color: $primary_color;
    }
  }
}