
        @import "src/styles/scss/common.scss";
        
.crypto-trade-steps {
    display: flex;
    justify-content: space-between;
    position: relative;
    margin-top: 16px;

    &::before {
        content: "";
        position: absolute;
        height: 1px;
        opacity: 0.20000000298023224;
        background: var(--unnamed, $primary_color);
        top: 50%;
        z-index: 0;
        left: 5px;
        right: 5px;
    }

    &__target {
        display: inline-block;
        width: 12px;
        height: 12px;
        background-color: $primary_color;
        border-radius: 50%;
    }

    &__icon-area {
        flex: 1;
        position: relative;
        display: flex;
        align-items: center;
        max-height: 20px;
    }

    &__up {
        color: var(--1, $font_color);
        text-align: center;
        font-family: PingFang SC;
        font-size: 14px;
        font-style: normal;
        font-weight: 400;
        line-height: normal;
    }

    &__down {
        color: var(--7, #F76260);
        text-align: center;
        font-family: PingFang SC;
        font-size: 14px;
        font-style: normal;
        font-weight: 400;
        line-height: normal;

    }

    &__item {
        display: flex;
        flex-direction: column;
        align-items: center;

        &--first {
            align-items: flex-start;

            .trade-steps {

                &__up,
                &__down {
                    color: var(--3, #888);
                    font-family: PingFang SC;
                    font-size: 14px;
                    font-style: normal;
                    font-weight: 400;
                    line-height: normal;
                }
            }
        }

        &--last {
            align-items: flex-end;
        }

        &--onstep {
            .trade-steps {

                &__up {
                    color: var(--1, $font_color);
                    text-align: center;
                    font-family: PingFang SC;
                    font-size: 14px;
                    font-style: normal;
                    font-weight: 500;
                    line-height: normal;
                }
            }
        }
        &--past {
            .trade-steps {

                &__down {
                    color: var(--unnamed, $primary_color);
                }
            }
        }
    }
}

