
        @import "src/styles/scss/common.scss";
        
.filter {
  color: #888;
  line-height: 20px;
  padding: $page_padding;
  background-color: $white;
  box-sizing: border-box;
  display: flex;
  align-items: center;

  border-bottom: $border_opacity;

  .filterOrders {
    font-size: 12px;
    color: #888888;
    padding: 0 14px 0 0;
    display: flex;
    align-items: center;
    position: sticky;
    top: 0;

    &>* {
      margin-right: 16px;
    }
  }

  .filterTags {
    flex: 1
  }
}

.filterPopup {
  padding-bottom: 40px;

  .filterPopupHeader {
    font-size: 14px;
    font-weight: 600;
    border-bottom: 1px solid rgba(1, 177, 78, .1);
    padding: 16px;
  }

  .filterPopupContent {
    padding: 16px;
  }

  .filterButton {
    font-size: 13px;
    line-height: 20px;
    width: 100%;
    border: 1px solid rgba(1, 177, 78, .1);

    &.active {
      background-color: rgba(1, 177, 78, .1);
    }

    &:active {
      background-color: rgba(1, 177, 78, .1);
    }
  }

  .filterButtonActions {
    margin-top: 10px;

    :last-of-type {
      .filterButtonAction {
        color: $white;
        background: linear-gradient(93.54deg, $submit_btn_bg_from 2.8%, $primary_color 94.95%);
      }

    }

    :first-of-type {
      .filterButtonAction {
        color: $primary_color;
        border: 1px solid $primary_color;
      }

    }

    .filterButtonAction {
      font-size: 12px;
      line-height: 20px;
      margin-bottom: 10px;
      width: 100%;
      margin-top: 6px;
    }
  }
}