
        @import "src/styles/scss/common.scss";
        
.i-block {
    padding: 12px 14px;
    background-color: $white;

    &--topline {
        border-top: 1px solid rgba($primary_color, 0.1);
    }

    &--nextby {
        padding-top: 0;
    }

    &__title {
        color: var(--1, $font_color);
        font-size: 16px;
        font-family: PingFang SC;
        font-style: normal;
        font-weight: 500;
        line-height: normal;
    }
}