
        @import "src/styles/scss/common.scss";
        
.i-payment-selector {

    margin-top: 8px;

    &__payment {
        color: var(--unnamed, $primary_color);
        text-align: justify;
        font-size: 12px;
        font-family: PingFang SC;
        font-style: normal;
        font-weight: 400;
        line-height: 24px;

        margin-left: 4px;
        display: flex;
        align-items: center;

        .i-icon {
            margin-right: 3px;
        }
    }

    &__app {
        margin-top: 12px;
        border-color: rgba($primary_color, 0.1);

        &-name,
        &-account {
            color: var(--1, $font_color);
            font-size: 14px;
            font-family: PingFang SC;
            font-style: normal;
            font-weight: 400;
            line-height: normal;

            flex: 1;

            span {
                color: var(--3, #888);
                font-family: PingFang SC;
                font-size: 14px;
                font-style: normal;
                font-weight: 400;
                line-height: normal;
            }
        }
        &-span{
            margin-right: 5px;
            display: block;
            max-width: 130px;
            text-overflow: ellipsis;
            overflow: hidden;
            white-space: nowrap;
        }
        &-pay-img{
            width: 56px;
            height: 56px;
        }

        .adm-list-item-content-main {
            display: flex;
            align-items: center;
            padding: 0;
            height: 44px;
        }
    }

    &__bank {
        margin-top: 12px;
        border-color: rgba($primary_color, 0.1);

        &-name {
            color: var(--1, $font_color);
            font-size: 14px;
            font-family: PingFang SC;
            font-style: normal;
            font-weight: 400;
            line-height: normal;

            flex: 1;
        }

        &-account {
            color: var(--3, #888);
            font-size: 14px;
            font-family: PingFang SC;
            font-style: normal;
            font-weight: 400;
            line-height: normal;

            margin-left: 16px;
            margin-right: 55px;
        }

        .adm-list-item-content-main {
            display: flex;
            align-items: center;
            padding: 0;
            height: 44px;
        }
    }

    &__wx-item,&__ali-item{
        .adm-list-item-content-main {
            height: 65px;
        }
    }

    .adm-collapse {
        .adm-collapse-panel-content {
            .adm-list-item-content .adm-list-item-content-main {
                padding: 0;

                .adm-list {
                    margin-top: 0;

                    .adm-list-body {
                        border: none;
                    }

                    .adm-list-item {
                        &:first-child {
                            .adm-list-item-content {
                                border-top: none;
                            }

                        }
                    }
                }
            }
        }

    }

    .adm-list-body,
    .adm-list-item-content {
        border-color: rgba($primary_color, 0.1);
    }

    .adm-check-list-item-extra {
        color: $primary_color;
    }

    .adm-selector-item {
        padding: 6px 12px;
        border-radius: 4px;
        border: 1px solid rgba($primary_color, 0.1);
        background: $white;

        &-active {
            border: 1px solid $primary_color;
        }
    }

    .adm-selector-check-mark-wrapper {
        border-bottom-color: $primary_color;
        border-right-color: $primary_color;
    }
}