
        @import "src/styles/scss/common.scss";
        
.i-passwordkit {

    &__input {
        &.adm-passcode-input {
            display: block;
            width: 330px;
            margin: 0 auto;
            padding: 12px 0;
        }

        .adm-passcode-input-cell-container {
            .adm-passcode-input-cell {
                width: 50px;
                height: 50px;
                flex-shrink: 0;

                border-radius: 10px;
                border: 1px solid $primary_color;

                &-focused {
                    box-shadow: 0 0 2px 0 $primary_color;
                }

                &-caret::after {
                    background: $primary_color;
                }
            }
        }
    }

    &__keyboard {
        width: 375px;
        display: flex;
        flex-wrap: wrap;
        gap: 14px;
        padding: 20px 14px;
        background-color: rgba(1, 177, 78, 1);
        margin: 0 auto;

        &-item {
            width: 106px;
            height: 50px;
            flex-shrink: 0;

            border-radius: 12px;
            background: rgba(241, 241, 241, 0.15);

            color: $white;
            text-align: center;
            font-family: PingFang SC;
            font-size: 24px;
            font-style: normal;
            font-weight: 600;
            line-height: 50px;

            border: none;

            &:active {
                background-color: rgba(241, 241, 241, 0.5);
            }

            &--nobg {
                background-color: transparent;
            }

            &--nokey {
                background-color: transparent;

                &:active {
                    background-color: transparent;
                }
            }
        }
    }

    &__popup {
        &-content {
            width: 375px;
            margin: 0 auto;
        }

        .adm-mask {
            background-color: transparent !important;
        }

        .adm-popup-body-position-bottom {
            border-top-left-radius: 18px;
            border-top-right-radius: 18px;
            overflow: hidden;
        }
    }
}