
        @import "src/styles/scss/common.scss";
        
.i-dynamicform{
  .adm-list-default .adm-list-body{
    border-color: rgba(1, 177, 78, .1);
  }
  .i-dynamicform__item{
    padding: 0 6px;
    .adm-list-item, .adm-list-item-content{
      padding: 0;
      border-top-color: rgba(1, 177, 78, .1);
      border-bottom-color: rgba(1, 177, 78, .1);
      border-color: rgba(1, 177, 78, .1);
    }

    .adm-list-item-content-prefix{
      padding-left: 2px;
      padding-right: 2px;
      color:$font_color;
      flex:3;
    }
    .adm-list-item-content-main{
      flex:4;
      padding-right: 2px;
    }
    .adm-list-item-content-extra{
      padding-right: 2px;
      padding-left: 2px;
    }
    
    .adm-form-item-label{
      color: $font_color;
    }
    font-size: 14px;
    .i-selectpicker,
    .i-formcurrent,
    .i-balancevb,
    .i-currentvb,
    .adm-input-element{
      font-size: 14px;
      text-align: right;
    }
    .i-formcurrent,
    .i-balancevb,
    .i-currentvb{
      color: $primary_color;
    }
    .adm-input-disabled{
      opacity: 1;
    }

    .adm-input-element{
      font-size: 14px;
      text-align: right;
    }
  }
}