
        @import "src/styles/scss/common.scss";
        
.i-earn-icon {
  position: sticky;
  width: 65px;
  height: 65px;
  bottom: 20vh;
  left: 80vw;
  z-index: 10;
  &__banner {
    position: absolute;
    height: inherit;
    width: 1px;
    right: 27.5px;
    height: 55px;
    bottom: 12px;
    background-image: url("~@/assets/images/float-banner.png");
    background-size: auto 100%;
    background-repeat: no-repeat;
    transition-property: width, right;
    transition-duration: 0.3s;
    transition-timing-function: linear;
    &.show {
      width: 200px;
    }
  }

  &__btn {
    width: 65px;
    height: 65px;
    background-image: url("~@/assets/images/float-btn.png");
    background-repeat: no-repeat;
    background-size: cover;
    position: relative;
    z-index: 1;
    border: none;
    background-color: transparent;
  
    &:active {
      transform: scale(0.9);
      transition: transform 0.1s;
    }
  }
}



