
        @import "src/styles/scss/common.scss";
        
.user_avatar {
  display: flex;
  justify-content: center;
  margin-bottom: 20px;
}

.avatar_row {
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
  margin: 20px 0;

  .avatar {
    position: relative;
    margin-bottom: 12px;
    .avatar_edit {
      position: absolute;
      bottom: 0;
      right: 0;
    }
  }
}