@mixin scrolling {
    -webkit-overflow-scrolling: touch;
}

.adm-button {

    &.flex,
    &[flex] {
        span {
            display: flex;
            align-items: center;
        }
    }

}

.common-form.adm-form {
    .adm-list {
        .adm-list-body {
            border-top: $border_opacity;
            border-bottom: $border_opacity;
        }

        .adm-list-item,
        .adm-list-item-content {
            padding: 0;
            border: none;
        }

        .adm-list-item-content {
            align-items: flex-end;
        }

        .adm-list-item-content-main {
            border-bottom: $border_opacity;

            padding: 10px 10px 16px;
        }

        .adm-list-item {
            &:last-of-type {
                .adm-list-item-content-main {

                    border-bottom: none;

                    padding: 10px;
                }
            }
        }
    }

    .adm-form-item .adm-list-item-title {
        font-weight: 500;
        font-size: 14px;
    }

    .adm-input-element,
    .adm-text-area-element {
        font-size: 14px;
        color: $primary_color;

    }

    .adm-input-element {
        line-height: 20px;
        height: auto;
    }

    .adm-form-item-child {
        font-size: 14px;
        color: $primary_color;
        margin: 10px 0 0;
    }

    .i-input {
        border-bottom: none;
        line-height: 20px;
        height: auto;

        &__content {
            padding: 0;
            line-height: 20px;
            height: auto;
            color: $primary_color
        }
    }

}

.common-submit-button {
    background: linear-gradient(93.54deg, $submit_btn_bg_from 2.8%, $primary_color 94.95%);
    width: 90%;
    display: block;
    margin: 10px auto !important;
    color: $white !important;
    font-size: 14px;
    line-height: 26px;
    display: block !important;
}

.tip-content {
    margin: 10px;
    text-align: center;
    font-size: 12px;
    color: #aaa;

    &-link {
        color: $primary_color;
        cursor: pointer;
    }

    &-icon {
        display: inline-block;
    }
}