
        @import "src/styles/scss/common.scss";
        
.sannerPopup {
  flex-direction: column;
  display: flex;
  align-items: center;
  justify-content: center;

  :global(.adm-popup-body) {
    border-radius: 20px 20px 0 0;
    padding: 040px 0;
    overflow: hidden;
  }
  
  #reader {
    width: 300px !important;
    height: 300px !important;
  }
  
  .btns {
    display: flex;
    justify-content: space-around;
    margin: 30px 0;
  }

  input[type=file] {
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    opacity: 0;
  }
}

.scanArea {
  width: 300px;
  height: 300px;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 0 auto;
  background-color: rgba(0, 0, 0, 0.3);
  video {
    max-width: 300px !important;
    max-height: 300px !important;
  }
}