
        @import "src/styles/scss/common.scss";
        
.i-payment-item {
  width: 100%;
  border-radius: 16px;
  overflow: hidden;
  transform: translate3d(0, 0, 0);

}

.i-payment-item__content {
  padding: 16px;
  height: 100px;

  &.item-1 {
    background-repeat: no-repeat;
    background-size: cover;

    &.item-bg0 {
      background: linear-gradient(90deg, #4faca2, #3b96b7);
    }

    &.item-bg1 {
      background: linear-gradient(90deg, #fd5660, #fe7465);
    }

    &.item-bg2 {
      background: linear-gradient(90deg, #ff7a43, #faa353);
    }

    &.item-bg3 {
      background: linear-gradient(90deg, #4579e6, #6797fb);
    }

    &.item-bg4 {
      background: linear-gradient(90deg, #10bf7a, #3fc69d);
    }

    &.item-bg8 {
      background: linear-gradient(90deg, #10bf7a, #3fc69d);
    }

    &.item-bg16 {
      background: linear-gradient(90deg, #10bf7a, #3fc69d);
    }
  }

  &.item-1 {
    background-repeat: no-repeat;
    background-size: 100%;
    background-image: url("~@/assets/images/payment/wechat-bg.png");
  }

  &.item-4 {
    background-repeat: no-repeat;
    background-size: cover;
    background-image: url("~@/assets/images/payment/wechat-bg.png");
  }

  &.item-2 {
    background-repeat: no-repeat;
    background-size: cover;
    background-image: url("~@/assets/images/payment/alipay-bg.png");
  }

  &.item-8 {
    background-repeat: no-repeat;
    background-size: cover;
    background-image: url("~@/assets/images/payment/wechat-bg.png");
  }

  &.item-16 {
    background-repeat: no-repeat;
    background-size: cover;
    background-image: url("~@/assets/images/payment/wechat-bg.png");
  }
}