
        @import "src/styles/scss/common.scss";
        
.page {
  height: 100%;
  max-height: 100%;
  display: flex;
  flex-direction: column;
  align-items: stretch;

  .tabs {
    padding-top: 10px;
    background-color: $white;

    :global(.adm-tabs-tab) {
      font-size: 16px;
      height: 32px;
      line-height: 22px;
      box-sizing: border-box;
      padding: 0;
      background-color: transparent;
      padding: 0 30px;

    }

    :global(.adm-tabs-content) {
      padding: 20px 0;
      box-sizing: border-box;
    }

    :global(.adm-tabs-tab-active) {
      color: $primary_color;

      box-sizing: border-box;

    }

    :global(.adm-tabs-tab-line) {
      background: $primary_color;

    }

    :global(.adm-tabs-header) {
      border: none
    }


    :global(.adm-list-body) {
      border: none;
      margin-top: 0;
      background-color: transparent
    }

  }
}

.content {
  flex: 1;
  overflow-y: auto;



  .title {
    font-size: 14px;
    line-height: 20px;
    // color: $font_color;
  }
  .titleTxt{
    margin-left: 4px;
  }
  
  .date {
    font-size: 12px;
    line-height: 24px;
    color: #888;
  }




}

.item {
  display: flex;
  cursor: pointer;
  padding: 10px;
  border-bottom: 1px solid rgba(43, 162, 69, .1);
  box-sizing: border-box;

  &.read {

    .title,
    .text {
      color: $font_color_tip;
    }
  }

  &.unRead {

    .title,
    .text {
      color: $font_color;
    }
  }

  .text {
    margin: 0 10px;
    flex: 1;
    display: flex;
    justify-content: space-between;
  }
  
}

.noticeBox{
  flex-flow: column;
  align-items: normal;
  .text{
    margin: 0;
  }
  .textCont{
    margin-top: 6px;
    font-size: 12px;
    color: #666;
    display: flex;
    justify-content: space-between;
    .textConts{
      flex: 1;
      padding-right: 8px;
      text-overflow: ellipsis;
      overflow: hidden;
      white-space: nowrap;
    }
  }
}