
        @import "src/styles/scss/common.scss";
        
.layout {
    height: 100%;
    background-repeat: no-repeat;
    background-size: 100%;
    background-image: url("~@/assets/images/login-bg.png");
    overflow: hidden;

    overflow-y: auto;

    :global(.i-header) {
        border: none;
        padding:30px;
    }
}

.logo {
    width: 220px;
    height: 72px;
    background-size: 100%;
    background-repeat: no-repeat;
    background-image: url("~@/assets/images/logo.png");

    margin: {
        top: 70px;
        left: 77.5px;
    }
}

.tabWrapper {
    margin-top: 52px;
    display: flex;
    align-items: center;
    justify-content: center;
}

.tabItem {
    width: 80px;
    height: 32px;
    line-height: 32px;
    text-align: center;
    color: $font_color_sub;
    font-size: 16px;
    font-weight: 600;
    position: relative;

    &:not(:first-child) {
        margin-left: 20px;
    }
}

.tabItemActive {
    color: $primary_color;

    &::after {
        display: block;
        content: '';
        border-bottom: 2px $primary_color solid;
    }
}

.appVersion {
    margin-top: 80px;
    margin-bottom: 70px;
    height: 17px;

    font-style: normal;
    font-weight: 400;
    font-size: 12px;
    line-height: 17px;
    text-align: center;
    /* identical to box height */


    color: #888888;
}