
        @import "src/styles/scss/common.scss";
        
.form {
    padding: {
        top: 24px;
        left: 40px;
        right: 40px;
    }
}


.input {
    margin-bottom: 24px;

    &:last-child {
        margin-bottom: 0;
    }
}

.icon {
    margin-left: 10px;
}
.dx_token{
    height: auto !important;
}

.imageCode {
    width: 92px;
    height: 34px;
}

.options {
    margin-top: 10px;

    display: flex;
    align-items: center;
    justify-content: space-between;

    font-style: normal;
    font-weight: 400;
    font-size: 12px;
    line-height: 17px;

    color: $font_color_sub;
}

.forgotPassword {
    font-style: normal;
    font-weight: 400;
    font-size: 12px;
    line-height: 17px;

    color: $font_color_sub;
    padding-right: 0;
}

.submitButton {
    margin-top: 60px;

    width: 295px;
    height: 44px;
    /* 渐变色1 */

    background: linear-gradient(93.54deg, $submit_btn_bg_from 2.8%, $primary_color 94.95%);
    border-radius: 60px;

    font-style: normal;
    font-weight: 500;
    font-size: 16px;
    line-height: 22px;

    color: $white;

}

.announcementDialog {
    width: 315px;
    height: 476px;

    background: $white;
    border-radius: 12px;
    overflow: hidden;
}

.announcementTitle {
    box-sizing: border-box;
    font-family: 'PingFang SC';
    font-style: normal;
    font-weight: 500;
    font-size: 16px;
    line-height: 22px;

    color: $primary_color;

    height: 92px;
    text-align: center;
    padding-top: 40px;

    position: relative;

    &::after,
    &::before {
        top: 51px;
        left: 74px;
        position: absolute;
        display: block;
        content: '';
        width: 40px;
        height: 1px;

        /* 主题色 */

        border-bottom: 1px solid $primary_color;
    }

    &::after {
        left: 202px;
    }
}

.announcementContent {
    position: relative;
    width: 259px;
    height: 306px;
    font-family: 'PingFang SC';
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 24px;
    /* or 171% */

    text-align: justify;

    color: $font_color_sub;
    padding: 0 28px;

    &::after {
        content: '';
        position: absolute;
        bottom: 0;
        width: 259px;
        height: 45px;
        background: linear-gradient(rgba(255, 255, 255, 0.001),
                white);
        /* transparent keyword is broken in Safari */
        pointer-events: none;
    }
}

.announcementContentScroller {
    box-sizing: border-box;
    width: 259px;
    height: 306px;
    overflow-y: auto;
    padding-bottom: 35px;
    padding-right: 8px;
}

.iReadAnnouncementButton {
    display: block;
    margin: 10px auto;
    width: 259px;
    height: 40px;

    /* 渐变色1 */

    background: linear-gradient(93.54deg, $submit_btn_bg_from 2.8%, $primary_color 94.95%);
    border-radius: 60px;

    font-family: 'PingFang SC';
    font-style: normal;
    font-weight: 500;
    font-size: 16px;
    line-height: 22px;

    color: $white;
}