
        @import "src/styles/scss/common.scss";
        
.i-popup {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate3d(-50%, -50%, 0);
    background-color: $white;
    overflow-y: auto;
    max-height: calc(100% - 10vh);

    &__mask {
        position: absolute;
        width: 100%;
        height: 100%;
        top: 0;
        right: 0;
        bottom: 0;
        left: 0;
        background-color: rgba($color: $black, $alpha: 0.25);
    }
}