
        @import "src/styles/scss/common.scss";
        
.area-picker-popup {
  .adm-popup-body {
    border-top-left-radius: 8px;
    border-top-right-radius: 8px;
    overflow: hidden;
  }

  .popup-hd {
    padding: 4px;
    border-bottom: 1px solid #eee;
  }

  .popup-bd {}
}

.input {
  cursor: pointer;
}

.actions {
  display: flex;
  justify-content: space-between;

  .action {
    cursor: pointer;

    &.action_submit {
      color: $primary_color
    }
  }
}