
        @import "src/styles/scss/common.scss";
        
.i-uploader {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;

    width: 347px;
    height: 126px;
    flex-shrink: 0;

    border-radius: 8px;
    border: 1px dashed var(--unnamed, $primary_color);

    margin: 0 auto 15px;

    position: relative;

    #QRcodeScanner {
        position: absolute;
        height: 0;
        width: 0;
        overflow: hidden;
    }

    &__loading {
        text-shadow: 0 2px white;
        font-size: 12px;
        color: #b8bec5;
    }

    &__text {
        color: var(--1, $font_color);
        text-align: justify;
        font-family: PingFang SC;
        font-size: 14px;
        font-style: normal;
        font-weight: 400;
        line-height: 24px;
        /* 171.429% */
        margin-top: 8px;
    }

    &__input {
        position: absolute;
        width: 347px;
        height: 126px;
        opacity: 0;
        .adm-image-uploader-upload-button-wrap{
            position: absolute;
            width: 100%;
            height: 100%;
        }
    }

    &__image {
        display: block;
        width: 347px;
        height: 126px;
        flex-shrink: 0;

        border-radius: 8px;
        border: 1px solid var(--unnamed, $primary_color);

        margin: 0 auto;
        text-align: center;
        vertical-align: middle;
        line-height: 126px;

        &-delete {
            position: absolute;
            top: 0;
            right: 0;
            background-color: rgba(100, 100, 100, 0.281);
            color: white;
        }
    }

}