
        @import "src/styles/scss/common.scss";
        
.listTable {
  :global(.adm-grid){
    height: 100%;
    align-items: center;
    align-content: center;
    justify-content: center;
    justify-items: center;
  }
  :global(.adm-grid-item) {
    font-size: 12px;
    text-align: center;
    
  }

  :global(.adm-list-body) {
    border: none;
  }

  :global(.adm-list-item-content-main) {
    padding: 0;
  }

  :global(.adm-list-item-content) {

    padding: 0 0;
    // border: none;
  }

  .list_row {
    height: 40px;
    line-height: 1.25;
    border-bottom: $border_opacity;
  }

  &.listTableHeader {
    .listTableHeaderItem {
      padding: 0 0;
      height: 32px;
      line-height: 32px;
      background-color: #e6f8ee;
      box-sizing: border-box;
    }

    :global(.adm-grid-item) {
      font-size: 10px;
      text-align: center;
    }
  }
}

.more {
  font-size: 10px;
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: center;
  line-height: 40px;
}