
        @import "src/styles/scss/common.scss";
        
.orderNo {
    text-align: center;
    margin: 40px auto;
}

.orderNoLabel {
    font-weight: bold;
    color: $black;
    text-align: center;
    font-family: PingFang SC;
    font-size: 16px;
    font-style: normal;
    font-weight: 500;
    line-height: 24px;
    /* 150% */
}

.amount {
    color: var(--unnamed, $primary_color);
    text-align: center;
    leading-trim: both;
    text-edge: cap;
    font-family: DINPro;
    font-size: 32px;
    font-style: normal;
    font-weight: 900;
    line-height: normal;
}

.amountLabel {
    text-align: center;
    color: $black;
    text-align: center;
    font-family: PingFang SC;
    font-size: 16px;
    font-style: normal;
    font-weight: 500;
    line-height: 24px;
    /* 150% */
    margin-top: 4px;
}

.payit {
    display: flex;
    margin: 10vh auto 0;
    width: 347px;
    height: 44px;
    justify-content: center;
    align-items: center;
    gap: 10px;
    flex-shrink: 0;

    border-radius: 8px;
    background: var(--1, linear-gradient(137deg, $submit_btn_bg_from 0%, $primary_color 100%));

    color: $white;
    text-align: center;
    font-family: PingFang SC;
    font-size: 16px;
    font-style: normal;
    font-weight: 500;
    line-height: normal;
}

.tips{
    margin-top: 24px;
    padding: 0px 20px;
    font-size: 13px;
    line-height: 20px;
    color: #bbb;
    text-align: center;
}