
        @import "src/styles/scss/common.scss";
        
.bg {
  background-repeat: no-repeat;
  background-size: 100%;
  background-image: url("~@/assets/images/about-bg.png");
  height: 167px;
  position: relative;

  .title {
    text-align: center;
    color: $white;
    font-size: 14px;
    line-height: 20px;
    padding-top: 16px;
  }

  .icon_bg {
    height: 86px;
    width: 86px;
    border-radius: 16px;
    background-color: $white;
    box-shadow: $shadow;
    position: absolute;
    bottom: 0;
    transform: translateX(-50%);
    left: 50%;
    display: flex;
    align-items: center;

    .logo {
      height: 60px;
      width: 60px;
      margin: 0 auto;
      background-repeat: no-repeat;
      background-size: 100%;
      background-image: url("~@/assets/images/logo-icon.png");
    }
  }
}

.h1 {
  font-size: 16px;
  font-weight: 500;
  line-height: 20px;
  text-align: center;
  color: $font_color;
  margin: 16px;
}

.content_wrapper {

  .content_header {
    .content_header_content {
      display: flex;
      align-items: center;
      justify-content: center;
    }

    .content_title_left {
      width: 50px;
      height: 10px;
      background-repeat: no-repeat;
      background-size: 100%;
      background-image: url("~@/assets/images/about/about-title-left.png");
    }

    .content_title_right {
      width: 50px;
      height: 10px;
      background-repeat: no-repeat;
      background-size: 100%;
      background-image: url("~@/assets/images/about/about-title-right.png");
    }

    .content_title {
      font-size: 16px;
      font-weight: 500;
      line-height: 22px;
      text-align: center;
      color: $primary_color;
      margin: 10px;
    }
  }


  .content_text {
    font-size: 14px;
    font-weight: 500;
    line-height: 24px;
    color: $font_color_sub;
    margin: 10px;

    p {
      margin-bottom: 16px;
    }
  }
}