
        @import "src/styles/scss/common.scss";
        
.card {
  box-sizing: border-box;
  display: flex;

  .item_text {
    flex: 1;
    font-weight: 500;
    font-size: 14px;
    line-height: 24px;
    color: $white;
    margin: 0 10px;
    word-break: break-all;
  }

  .item_icon {
    height: 32px;
    width: 32px;
    border-radius: 32px;
  }


  .weight {
    font-weight: 500;
  }



  .item_code {
    height: 70px;
    width: 70px;
    display: none;
    display: block;

    :global(.adm-image) {
      height: 100%;
      width: 100%;
    }
  }

  .item_icon {
    width: 28px;
    height: 28px;


  }
}