
        @import "src/styles/scss/common.scss";
        
.approval_err {
  height: 140px;
  width: 140px;
  margin: 20px auto;
  background-repeat: no-repeat;
  background-size: 100%;
  background-image: url("~@/assets/images/common/approval_err.png");
}

.approval_err_text,
.approvaling_text {
  font-size: 16px;
  line-height: 22px;
}

.approval_err_text {
  text-align: center;
  color: #F76260;
}

.approvaling_text {
  text-align: center;
  color: $primary_color;
}

.approvaling {
  margin: 20px auto;
  height: 14px;
  width: 14px;
  background-repeat: no-repeat;
  background-size: 100%;
  background-image: url("~@/assets/images/common/approvaling.png");
}