
        @import "src/styles/scss/common.scss";
        
.payment-info {
    &__item {
        display: flex;
        height: 24px;
        line-height: 24px;
        margin-top: 12px
    }

    &__label {
        color: var(--2, $font_color_sub);
        text-align: justify;
        font-family: PingFang SC;
        font-size: 14px;
        font-style: normal;
        font-weight: 400;
        line-height: 24px;
        /* 171.429% */
        min-width: 80px;
    }

    &__value {
        color: var(--2, $font_color_sub);
        font-family: PingFang SC;
        font-size: 14px;
        font-style: normal;
        font-weight: 400;
        line-height: 24px;
        /* 171.429% */
        flex: 1;
    }

    &--QRCode {
        display: flex;
    }

    &__info {
        flex: 1;
    }

    &__qrcode {
        width: 80px;
        height: 80px;
        margin-left: 10px;
    }
}