
        @import "src/styles/scss/common.scss";
        
.itemWrapper {
    margin: 12px auto 0;
    width: 347px;
    height: 120px;
    flex-shrink: 0;

    border-radius: 12px;
    background: $white;
    box-shadow: 0px 4px 4px 0px rgba(204, 223, 206, 0.25);
}

.top {
    display: flex;

    padding: {
        top: 10px;
        left: 12px;
        right: 12px;
    }
}

.avatar {
    width: 40px;
    height: 40px;
    flex-shrink: 0;
    border-radius: 50%;
    overflow: hidden;
    &.defaultAvatar {
        background-repeat: no-repeat;
        background-size: 100%;
        background-image: url("~@/assets/avatar/avatar0.png");
      }
}

.info {
    flex: 1;
    display: flex;
    padding: {
        left: 5px;
    }
}

.infoTop {
    display: flex;
    align-items: center;
}

.name {
    white-space: nowrap;

    color: $font_color;
    font-size: 14px;
    font-weight: 500;
}

.payments {
    display: flex;
    align-items: center;
}

.currency {
    margin-top: -2px;
}

.levelWrapper {
    display: flex;
    align-items: center;

    padding: {
        top: 6px;
    }
}

.levelLabel {
    width: 50px;

    color: $font_color;
    font-size: 12px;
}

.level {
    display: flex;
    align-items: center;
}

.status {}

.balance {
    display: flex;
    align-items: center;

    padding: {
        top: 2px;
    }
}

.balanceValue {
    flex-shrink: 0;

    color: var(--7, #F76260);
    text-align: right;
    font-size: 14px;
    font-weight: 700;

    margin-right: 5px;
}

.orderType {

    text-align: right;
    font-size: 12px;
    font-weight: 500;

    padding: {
        top: 5px;
    }
}

.orderType__separably {
    color: $primary_color;
}

.orderType__inseparably {
    color: #F76260;
}

.bottom {
    display: flex;
}

.bottomItem {
    flex: 1;
    height: 68px;

    padding: {
        top: 16px;
    }

}

.bottomItemLabel {
    color: #888;
    text-align: center;
    font-size: 12px;
}

.bottomItemValue {
    color: $font_color;
    text-align: center;
    font-size: 16px;
    font-weight: 700;
}