
        @import "src/styles/scss/common.scss";
        
.orderItem {
  border-radius: 12px;
  background-color: $white;
  margin: 16px;
  box-shadow: 0 3px 20px #cccccc80;
  overflow: hidden;
  display: flex;



  .orderStatus {
    width: 32px;

    color: $white;
    display: flex;
    align-items: center;

    .orderStatusText {
      width: 12px;
      font-size: 12px;
      line-height: 14px;
      margin: 0 auto;
      text-align: center;
    }
  }

  .orderItemInfo {
    flex: 1;
    padding: 10px;
    position: relative;


    .orderItemInfoDetail {
      border-top: 1px dashed $primary_color;
      display: flex;
      align-items: flex-end;
      justify-content: space-between;
    }
  }

  .orderAmount {
    font-size: 24px;

    padding: 10px 0;
    font-weight: 700;
    color: $primary_color;
    vertical-align: bottom;
    display: flex;
    align-items: baseline;

    .icon {
      background-position: bottom;
      margin: 0 6px
    }
  }

  .orderTime {
    color: #888;
    font-size: 12px;
  }

  .orderNo {
    color: #888;
    font-size: 12px;
    padding-bottom: 4px;
  }

  .orderProcessButton {
    width: 88px;
    text-align: center;
    border: 1px solid $primary_color;
    color: $primary_color;
    font-size: 14px;
    border-radius: 60px;
    line-height: 32px;
    height: 32px;
    cursor: pointer;
  }

  .orderType {
    position: absolute;
    right: 0;
    top: 0;
    height: 50px;
    width: 50px;
    font-size: 12px;
    color: $white;
  }

  .orderTypeText {
    position: absolute;
    right: 16px;
    top: 16px;
    // &.sell {
    //   background-repeat: no-repeat;
    //   background-size: 100%;
    //   background-image: url("~@/assets/images/orders/sell_sign.png");
    // }

    // &.buy {
    //   background-repeat: no-repeat;
    //   background-size: 100%;
    //   background-image: url("~@/assets/images/orders/buy_sign.png");
    // }
  }

  .orderTypeBg {
    width: 80px;
    height: 20px;
    transform: translate(-12px, 12px) rotate(45deg);

    &.sell {
      background-color: #EA1A17;
      box-shadow: rgba(153, 153, 153, 0.1);
    }

    &.buy {
      background-color: #02C658;
      box-shadow: rgba(153, 153, 153, 0.1);
    }
  }

}