@import-normalize;
/* bring in normalize.css styles */

/* rest of app styles */

@import "./reset.css";

/* Global style */
* {
  user-select: none;
  box-sizing: border-box;
}

/* 输入框类型组件如果无法选取则无法拷贝 同时ios无法输入, 5+ app也会出现无法拷贝粘贴的情况 */
input,
textarea,
*[contenteditable="true"] {
  user-select: auto;
}

body {
  font-size: 16px;
  max-width: 3.75rem;
  margin: 0 auto;
  background-color: rgb(233, 233, 233);
}

#detect-height {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: -100;
}

#root {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fff;
  height: 100%;
  overflow: hidden;
  position: relative;
}

#root .page {
  display: flex;
  flex-direction: column;
  overflow: hidden;
}
