
        @import "src/styles/scss/common.scss";
        
.i-tab-bar {
    position: fixed;
    width: 100%;
    max-width: 375px;
    
    bottom: 0px;
    left: 50%;
    transform: translateX(-50%);

    background: $white;
    // border-radius: 12px;
    border-top: 1px solid rgba(43, 162, 69, .1);

    

    padding: {
        left: 28px;
        right: 28px;
        // top: 9px;
        // bottom: 23px;
    }
    &__content{
        display: flex;
        height: 52px;
        justify-content: space-between;
        box-sizing: border-box;
        align-items: center;
    }

    &__item{
        text-align: center;
        &_text{
            font-size: 10px;
            line-height: 14px;
        }
        
    }
    &__item--active{
        .i-tab-bar__item_text{
            color: $primary_color
        }
    }

    &__scan{
        position: relative;
        height: 54px;
        width: 54px;
        &_icon{
            position: absolute;
            left: 50%;
            top:0;
            transform: translateY(-34px) translateX(-50%);
            height: 108px;
            width: 108px;
            position: absolute;
            background-repeat: no-repeat;
            background-size: 100%;
            vertical-align: middle;
            background-image: url("~@/assets/icons/root/root-home-scan.png");
        }
    }
}