
        @import "src/styles/scss/common.scss";
        
.content {
    position: relative;
    display: flex;
}

.board {
    padding-bottom: 55px;
}

.boardBgTitle {
    color: $white;
    font-family: PingFang SC;
    font-size: 16px;
    font-style: normal;
    font-weight: 500;
    line-height: normal;

    margin-top: 16px;
}

.boardBgDesc {
    color: $white;
    font-family: PingFang SC;
    font-size: 12px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;

    margin-top: 4px;
    white-space: pre-wrap;
}

.boardBgDescHighlight {
    color: red;
    font-weight: bold;
}

.boardContent {
    padding-top: 46px;
    padding-bottom: 16px;
}

.boardContentOrderNumber {
    color: var(--3, #888);
    text-align: center;
    font-family: PingFang SC;
    font-size: 12px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;

    display: flex;
    align-items: center;
    justify-content: center;

    margin-top: 8px;
}

.validAmount {
    color: $black;
    text-align: center;
    font-size: 16px;
    font-family: PingFang SC;
    font-style: normal;
    font-weight: 500;
    line-height: 24px;
}

.amount {
    color: var(--unnamed, $primary_color);
    text-align: center;
    leading-trim: both;
    text-edge: cap;
    font-size: 32px;
    font-family: DINPro;
    font-style: normal;
    font-weight: 900;
    line-height: normal;
}

.amountIcon {
    width: 28px;
    height: 14px;
    vertical-align: baseline;
}

.formRow {
    display: flex;
    align-items: center;
    margin-top: 12px
}

.formLabel {
    color: var(--2, $font_color_sub);
    text-align: justify;
    font-family: PingFang SC;
    font-size: 14px;
    font-style: normal;
    font-weight: 400;
    line-height: 24px;
    /* 171.429% */
    min-width: 80px;
}

.paymentOptions {
    :global(.adm-selector-item) {
        padding: 6px 12px;
        border-radius: 4px;
        border: 1px solid rgba($primary_color, 0.1);
        background: $white;
    }

    :global(.adm-selector-item-active) {
        border: 1px solid $primary_color;
    }

    :global(.adm-selector-check-mark-wrapper) {
        border-bottom-color: $primary_color;
        border-right-color: $primary_color;
    }
}

.formValue {
    flex: 1;
    color: var(--2, $font_color_sub);
    text-align: justify;
    font-family: PingFang SC;
    font-size: 14px;
    font-style: normal;
    font-weight: 400;
    line-height: 24px;
    /* 171.429% */
}

.blockTitle {
    display: flex;
    align-items: center;
}

.vcoinNumber {
    color: var(--unnamed, $primary_color);
    leading-trim: both;
    text-edge: cap;
    font-family: DINPro;
    font-size: 24px;
    font-style: normal;
    font-weight: 700;
    line-height: normal;

    padding-left: 17px;
}

.vcoin {
    margin-left: 8px;
}

.options {
    display: flex;
    padding: 14px;
}

.cancelSellingBtn {
    flex: 1;
    height: 44px;
    justify-content: center;
    align-items: center;
    gap: 4px;
    flex-shrink: 0;

    border-radius: 8px;
    background: var(--2, linear-gradient(133deg, #FF7D45 0%, #FF5654 100%));

    color: $white;
    text-align: center;
    font-family: PingFang SC;
    font-size: 16px;
    font-style: normal;
    font-weight: 500;
    line-height: normal;
}

.tableList {
    :global(.adm-list-header) {
        padding-left: 0;
        padding-right: 0;
    }
}


@mixin col1 {
    width: 100px
}

@mixin col2 {
    flex: 1;
}

@mixin col3 {
    width: fit-content;
}

@mixin col4 {
    flex: 1;
}

.tableTitle {
    display: flex;
    align-items: center;
    justify-content: space-between;
    line-height: 32px;
    height: 32px;

    color: var(--1, $font_color);
    text-align: center;
    font-family: PingFang SC;
    font-size: 12px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;

    background: rgba($primary_color, 0.1);

    padding-right: 19px;
}

.tableHeaderColTime {
    @include col1;
}

.tableHeaderColAmount {
    @include col2;

    span {
        color: var(--unnamed, $primary_color);
        font-family: PingFang SC;
        font-size: 12px;
        font-style: normal;
        font-weight: 400;
        line-height: normal;
    }
}

.tableHeaderColPayment {
    @include col3
}

.tableHeaderColStatus {
    @include col4
}

.tableListItem {
    padding-left: 0;

    :global(.adm-list-item-content) {
        padding-right: 0;
    }
}

.tableRow {
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.tableColTime {
    color: var(--2, $font_color_sub);
    text-align: center;
    font-family: DINPro;
    font-size: 14px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;

    @include col1;
}

.tableColAmount {
    color: $font_color;
    text-align: center;
    font-family: DINPro;
    font-size: 14px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;

    @include col2;
}

.tableColPayment {

    @include col3
}

.tableColStatus {
    color: var(--6, #F90);
    text-align: center;
    font-family: PingFang SC;
    font-size: 12px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;

    @include col4
}