
        @import "src/styles/scss/common.scss";
        
.description_page{
  padding: 10px;
  line-height: 2;
  a{
    color: $primary_color;
  }
}
.description_title{
  font-family: PingFang SC;
  font-size: 16px;
  font-weight: 600;
  line-height: 22px;
  letter-spacing: 0em;
  text-align: left;
  margin-top: 10px;
}

.description_title2{
  font-family: PingFang SC;
  font-size: 14px;
  font-weight: 400;
  line-height: 20px;
  letter-spacing: 0em;
  text-align: left;
  margin-top: 10px;
  span{
      color: $primary_color
  }
}
.description_content{
  &2{
    font-size: 14px;
  }
}
.description_block{
  padding:10px;
  line-height: 1.5;
  .description_title{
      font-family: PingFang SC;
      font-size: 16px;
      font-weight: 600;
      line-height: 22px;
      letter-spacing: 0em;
      text-align: left;
  }
}
.description_highlight{
  color :$primary_color;
}
.description_table{
  border-color:$primary_color;
  width: 100%;
  background:  rgba(104, 255, 170, 0.16);
  border-radius: 10px;
  overflow: hidden;
  th{
      font-family: PingFang SC;
      font-size: 14px;
      font-weight: 600;
      line-height: 20px;
      letter-spacing: 0em;
      text-align: center;
      border-right: 2px solid rgba(1, 177, 78, .1);
      border-bottom: 2px solid rgba(1, 177, 78, .1);
      vertical-align: middle;
      padding: 8px 4px;
      &:last-of-type{
          border-right: none;
      }
  }
  td{
      font-family: PingFang SC;
      font-size: 12px;
      font-weight: 400;
      line-height: 20px;
      letter-spacing: 0em;
      text-align: center;
      vertical-align: center;

      border-right: 2px solid rgba(1, 177, 78, .1);
      border-bottom: 2px solid rgba(1, 177, 78, .1);
      vertical-align: middle;
      &:last-of-type{
          border-right: none;
      }
      padding: 8px 4px;
      span{
          font-size: 12px;
          color: $font_color_sub;
      }
  }
  tr:last-of-type{
      td{
          border-bottom: none;
      }
      
  }
  tbody{
      tr:nth-of-type(2n){
          background-color: rgba(0, 227, 99, .4);
      }
  }
}

.description_dialog_table{
  border-radius: 0px;
  border-bottom-left-radius: 8px;
  border-bottom-right-radius: 8px;
  // background: #FBE0C1;
  background-image: url("~@/assets/images/merchant/subsidy/a_bg.png");
  background-size: 100% ;
  thead{
    background: #4ce417;
    th{
      font-size: 12px;
      line-height: 10px;
    }
  }
 
  th,td{
    border-right: 1px solid #0f6303;
    border-bottom: 1px solid #0f6303;
  }
  tbody{
    tr{
      background: #FCF0DF !important;
    }
  }

  .headLine {
    // background:  transparent url("data:image/svg+xml;base64,");
    background: transparent url("data:image/svg+xml;base64,");
    
    .leftBottom {
      display: block;
      font-size: 12px;
      color: #000;
      float: left;
      margin-left: 5px;
      margin-top: 15px;
    }
    .rightTop {
      display: block;
      font-size: 12px;
      color: #000;
      float: right;
      margin-right: 5px;
    }
  }
}


.earn_bg_box{
  position: relative;
  margin-top: 12px;
  height: 230px;
  text-align: center;
  background-image: url("~@/assets/images/merchant/subsidy/bg.png");
  background-size: 85%;
  background-repeat: no-repeat;
  background-position: center;
  .earn_logo{
    position: absolute;
    top: 5px;
    left: 50%;
    right: 50%;
    height: 46px;
    transform: translate(-50%);
  }
  .earn_txt{
    position: absolute;
    bottom: 25px;
    height: 30px;
    line-height: 30px;
    background-image: url("~@/assets/images/merchant/subsidy/txt_bg1.png");
    background-size: 70%;
    background-repeat: no-repeat;
    background-position: center;
    img{
      width: 60%;
    }
  }
  .earn_txt2{
    position: absolute;
    bottom: 0;
    width: 100%;
    height: 20px;
    // background-image: url("~@/assets/images/merchant/subsidy/txt_bg2.png");
    background-size: 100%;
    background-repeat: no-repeat;
    text-align: center;
    img{
      height: 14px;
    }
  }
}
