
        @import "src/styles/scss/common.scss";
        
.i-notification {
    position: fixed;
    z-index: 2000;
    top: 10px;
    left: 50%;
    transform: translateX(-50%);
    background: rgba(#C5D7D8, 0.8);
    box-shadow: 0px 0px 22px 4px rgba(173, 168, 173, 0.61);
    border-radius: 14px;
    padding: 10px;
    display: flex;
    flex-direction: column;
    border: 1px solid #C5D7D8;
    overflow: hidden;

    &::before {
        content: '';
        position: absolute;
        z-index: 0;
        top: 0;
        right: 0;
        bottom: 0;
        left: 0;
        filter: blur(10px);
        background: #C5D7D8;
        overflow: hidden;
    }

    * {
        position: relative;
    }

    &__top {
        display: flex;
    }

    &__icon {
        &:last-child {
            margin-left: auto;
            color: #8c9797;
            font-size: 12px;
        }
    }

    &__title {
        margin-left: 8px;
    }

    &__content {
        font-size: 12px;
        line-height: 1.25;
        padding-top: 5px;
        color: #333;
    }
}