
        @import "src/styles/scss/common.scss";
        
.wrapper {
  display: flex;
  flex-direction: column;
  align-items: stretch;

  .header {
    padding: $page_padding;
    color: $font_color_light;
    font-size: 14px;
    line-height: 20px;

  }

  .content {
    flex: 1;
    overflow-y: auto;
  }

  .tip {
    padding: $page_padding;
  }

  .item {
    display: flex;
    background-color: $white;
    margin: 10px 0;
    padding: 10px 10px;
    font-size: 14px;
    line-height: 24px;
    box-sizing: border-box;

    .device_info {
      flex: 1;
      display: flex;
    }

    .item_type {
      color: $primary_color;
      font-size: 16px;
    }

    .item_no {
      color: $font_color_light;
      margin: 0 10px;
    }

    .item_active {
      flex: 1;
      margin: 0 10px;
      color: $font_color_light;
    }
  }
}