
        @import "src/styles/scss/common.scss";
        
.page {
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: stretch;

  :global(.adm-tabs-tab-active) {
    color: $primary_color;

    box-sizing: border-box;

  }
  .dealinfo{
    :global(.adm-capsule-tabs-header){
      margin: 0px;
      padding: 0px;
    }
    :global(.adm-capsule-tabs-tab-wrapper){
      width: 30%;
      margin: 2px;
      padding: 2px;
    }
    :global(.adm-capsule-tabs-tab){
      font-size: 12px;
    }
    .img{
      width: 100%;
    }
  }


  :global(.adm-capsule-tabs-tab) {
    font-size: 013px;
    border-radius: 8px;
    line-height: 040px;
    box-sizing: border-box;
    padding: 0;
    background-color: transparent;
    border: 1px solid $primary_color;
  }

  :global(.adm-capsule-tabs-content) {

    box-sizing: border-box;
    padding: 0;
  }

  :global(.adm-capsule-tabs-tab-active) {
    background-repeat: no-repeat;
    background-size: 100%;
    background-color: $primary_color;
    border: none;
  }

  :global(.adm-capsule-tabs-tab-list) {
    flex-wrap: wrap;
  }

  :global(.adm-capsule-tabs-tab-wrapper) {
    width: 30%;
    margin: 6px 2px;
  }
}

.content_header {
  .content_header_content {
    display: flex;
    align-items: center;
    justify-content: center;
  }

  .content_title_left {
    width: 50px;
    height: 16px;
    background-repeat: no-repeat;
    background-size: 100%;
    background-image: url("~@/assets/images/helps/title-l.png");
  }

  .content_title_right {
    width: 50px;
    height: 16px;
    background-repeat: no-repeat;
    background-size: 100%;
    background-image: url("~@/assets/images/helps/title-r.png");
  }

  .content_title {
    font-size: 16px;
    font-weight: 500;
    line-height: 22px;
    text-align: center;
    color: $primary_color;
    margin: 10px;
  }
}

.content_wrapper {
  flex: 1;
  overflow-y: auto;


  .content_text {
    font-size: 14px;
    font-weight: 500;
    line-height: 24px;
    color: $font_color_sub;
    margin: 10px;

    p {
      margin-bottom: 16px;
    }
  }
}