
        @import "src/styles/scss/common.scss";
        
$px: 25px;

.pageLinkAccount {
    height: 100%;
    background-repeat: no-repeat;
    background-size: 100%;
    background-image: url("~@/assets/images/mask-bg.png");
    overflow-y: auto;
}

.header {
    box-shadow: 4px 0px 4px 0 rgba(0, 0, 0, 0.12);
    align-items: center;
}

.headerRightBtn {
    background-color: transparent;
    border: none;
}

.headerRightIcon {
    color: $primary_color;
}

.item {
    font-size: 13px;
    line-height: 40px;
    padding-left: $px;
    padding-right: $px;
    font-weight: 600;
    border-bottom: 1px solid rgba($primary_color, 0.3);
    white-space: nowrap;
}

.confirmLink {
    border-radius: 8px;
}

.warning {
    padding: {
        left: $px;
        right: $px;
    }

    color: $warn;
    font-size: 14px;

    span {
        font-weight: 600;
        color: $darker_warn;
    }
}