
        @import "src/styles/scss/common.scss";
        
.sub_page {
  position: absolute;
  width: 100%;

  .sub_main {
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: stretch;
  }

  .sub_page_header {
    border-bottom: $border_opacity;
  }

  .sub_page_content {
    background-color: $sub_page_bg_color;
    flex: 1;
    overflow-y: auto;
  }

  .sub_page_footer {
    // position: fixed;
    // bottom: 5px;
    // width: 100%;
    border-top: $border_opacity;
  }

  &.bg {
    background-repeat: no-repeat;
    background-size: 100%;
    background-image: url("~@/assets/images/mask-bg.png");

    .sub_page_content {
      background-color: transparent;
    }

    .sub_page_header {
      border-bottom: none;
    }
  }

}