
        @import "src/styles/scss/common.scss";
        
.i-header {
    display: flex;
    align-items: center;
    width: 375px;
    height: 44px;
    padding: 0 4px;
    border-bottom: 1px solid rgba(#2BA245, 0.1);

    &__title {
        color: $font_color_sub;
        text-align: center;
        font-size: 16px;
        font-style: normal;
        font-weight: 400;
    }

    &__actions {
        flex: 1;
        display: flex;

        &--right {
            justify-content: flex-end;
        }
    }

    &_action {
        height: 44px;
        min-width: 44px;
        display: flex;
        align-items: center;
        justify-content: center;

        >span {
            display: flex;
            align-items: center;
            justify-content: center;
        }
    }
}