
        @import "src/styles/scss/common.scss";
        
.main {
    height: 100%;
    background: $sub_page_bg_color;
}

.boardBackground {
    background: linear-gradient(to right, #05c157, #0cd259);
    padding: 24px 20px 0;
    position: relative;
}

.content {
    display: flex;
    align-items: center;
}

.avatar {
    display: inline-block;
    width: 40px;
    height: 40px;
    border-radius: 50%;
    margin-right: 5px;
}

.username {
    color: $font_color;
    font-size: 16px;
    font-family: PingFang SC;
    font-style: normal;
    font-weight: 500;
    line-height: normal;
}

.levelWrapper {
    display: flex;
    align-items: center;
}

.levelLabel {
    color: $font_color;
    font-size: 12px;
    font-family: PingFang SC;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
    margin-right: 5px;
}

.orderType {
    box-sizing: border-box;
    width: 73px;
    height: 24px;
    background-color: $primary_color;
    border-top-left-radius: 12px;
    border-bottom-left-radius: 12px;
    position: absolute;
    right: 0;
    top: 16px;
    line-height: 24px;

    color: $white;
    text-align: center;
    font-family: PingFang SC;
    font-size: 14px;
    font-style: normal;
    font-weight: 500;
}

.boardContent {
    padding-top: 46px;
    padding-bottom: 16px;
}

.validAmount {
    color: $black;
    text-align: center;
    font-size: 16px;
    font-family: PingFang SC;
    font-style: normal;
    font-weight: 500;
    line-height: 24px;
}

.amount {
    color: var(--unnamed, $primary_color);
    text-align: center;
    leading-trim: both;
    text-edge: cap;
    font-size: 32px;
    font-family: DINPro;
    font-style: normal;
    font-weight: 900;
    line-height: normal;
}

.amountIcon {
    width: 28px;
    height: 14px;
    vertical-align: baseline;
}

.lineOfSellerInfo {
    margin-top: 8px;
    display: flex;
    justify-content: space-between;
}

.itemOfSellerInfo {
    flex: 1;
    display: flex;

    font-size: 12px;
    font-family: PingFang SC;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
}

.keyOfSellerInfo {
    color: #888;
}

.valueOfSellerInfo {
    color: $primary_color;
}

.buyAmountInput {
    margin-top: 8px;

    color: $primary_color;
    font-size: 18px;
    font-family: DINPro;
    font-style: normal;
    font-weight: 700;
    line-height: normal;
}

.confirmButton {
    display: flex;
    width: 347px;
    height: 44px;
    justify-content: center;
    align-items: center;
    gap: 10px;
    flex-shrink: 0;
    margin: 0 auto;

    color: $white;
    text-align: center;
    font-family: PingFang SC;
    font-size: 16px;
    font-style: normal;
    font-weight: 500;
    line-height: normal;

    border-radius: 8px;
    background: var(--1, linear-gradient(137deg, $submit_btn_bg_from 0%, $primary_color 100%));
}

.warning {
    margin-top: 10px;
    color: var(--4, #AAA);
    font-family: PingFang SC;
    font-size: 14px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
    text-align: center;
}