
        @import "src/styles/scss/common.scss";
        
.selling-trade-board-bg {
    &.Hanging {
        background: linear-gradient(145deg, #05C057 0%, #00E865 100%);
    }

    &.WaitingSellerConfirm {
        background: linear-gradient(145deg, #6dadff 0%, #108fff 100%);
    }

    &.SellerCancelsPurchase {
        background: linear-gradient(145deg, #FF7B69 0%, #FFAC30 100%);
    }

    &.SaleOut {
        background: linear-gradient(145deg, #FF7B69 0%, #FFAC30 100%);
    }

}

.selling-trade-board-status {

    background-size: contain;
    background-repeat: no-repeat;

    &.Hanging {
        margin-top: 9px;
        margin-left: 21px;
        margin-right: 28px;
        width: 103px;
        height: 103px;
        background-image: url('~@/assets/images/trade/Hanging.png');
    }

    &.SaleOut {
        margin-top: 9px;
        margin-left: 21px;
        margin-right: 28px;
        width: 103px;
        height: 103px;
        background-image: url('~@/assets/images/trade/Hanging.png');
    }



    &.WaitingSellerConfirm {
        margin-right: -20px;
        width: 162px;
        height: 120px;
        background-image: url('~@/assets/images/trade/WaitingSellerConfirm.png');
    }


    &.SellerCancelsPurchase {
        width: 86px;
        height: 96px;

        margin: {
            top: 18px;
            left: 33px;
            right: 33px;
        }

        background-image: url('~@/assets/images/trade/SellerCancelsPurchase.png');
    }

}

.selling-order-type {
    width: 73px;
    height: 24px;
    flex-shrink: 0;


    border-top-left-radius: 12px;
    border-bottom-left-radius: 12px;

    position: absolute;
    top: 16px;
    right: 0;

    color: $white;
    text-align: center;
    font-family: PingFang SC;
    font-size: 14px;
    font-style: normal;
    font-weight: 500;
    line-height: normal;
    text-align: center;
    line-height: 24px;

    &.separably {
        background-color: $primary_color;
    }

    &.inseparably {
        background: linear-gradient(133deg, #FF7D45 0%, #FF5654 100%);
    }
}