
        @import "src/styles/scss/common.scss";
        
.popup {
  :global(.adm-popup-body-position-bottom) {
    border-radius: 16px 16px 0 0;
    overflow: hidden;
  }

  .filter {
    color: #888;
    line-height: 20px;
    padding: $page_padding;
    background-color: $white;
    box-sizing: border-box;
    display: flex;
    align-items: center;

    border-bottom: $border_opacity;

    .filterTags {
      flex: 1
    }
  }

  .filterPopup {
    padding-bottom: 20px;

    .filterPopupHeader {
      font-size: 14px;
      font-weight: 600;
      border-bottom: 1px solid rgba(1, 177, 78, .1);
      padding: 16px;
    }

    .filterPopupContent {
      padding: 16px;
    }

    .filterButton {
      font-size: 13px;
      line-height: 20px;
      width: 100%;
      border: 1px solid rgba(1, 177, 78, .1);

      &.active {
        background-color: rgba(1, 177, 78, .1);
      }

      &:active {
        background-color: rgba(1, 177, 78, .1);
      }
    }

    .filterButtonActions {
      margin-top: 10px;

      :last-of-type {
        .filterButtonAction {
          color: $white;
          background: linear-gradient(93.54deg, $submit_btn_bg_from 2.8%, $primary_color 94.95%);
        }

      }

      :first-of-type {
        .filterButtonAction {
          color: $primary_color;
          border: 1px solid $primary_color;
        }

      }

      .filterButtonAction {
        font-size: 12px;
        line-height: 20px;
        margin-bottom: 10px;
        width: 100%;
        margin-top: 6px;
      }
    }
  }
}

.filterRow {
  max-height: 320px;
  overflow: auto;
  .filterTitle {
    font-size: 14px;
    font-weight: 500;
    margin: 10px 0;
  }
}