
        @import "src/styles/scss/common.scss";
        
.main {
    font-size: 12px;
    font-family: "PingFang SC", -apple-system, "Helvetica Neue", "Helvetica", "Arial", "Lucida Grande", sans-serif;
    line-height: 1.2;
}

.gradeArea {
    padding: 30px 67px;


    .grades {
        display: flex;
        align-items: center;
        justify-content: space-around;
        padding-bottom: 30px;

        :global(.i-level__icon) {
            width: 35px;
            height: 40px;
            background-size: contain;
        }

        .grade {
            width: 35px;
            height: 40px;
            background-size: 100% 100%;


        }
    }

    .subtitle {
        display: flex;
        justify-content: space-between;
        padding-top: 10px;
        font-size: 12px;
        color: #333;
    }

    .progressBar {
        // color: #6F5BFEFF;
    }
}

.content {
    padding: 12px;
    font-size: 12px;

    .title {
        font-size: 15px;
        font-weight: 600;
    }

    .content {
        padding: 12px 0;

        .grades {
            display: flex;
            align-items: center;
            justify-content: space-around;
            padding: 15px 0;

            .item {
                white-space: nowrap;
                font-size: 11px;
                text-align: center;

                .grade {
                    width: 52px;
                    height: 60px;
                    background-repeat: no-repeat;
                    background-size: contain;
                    margin-bottom: 5px;
                }
            }
        }

        .examples {
            .group {
                margin-top: 20px;

                .row {
                    display: flex;
                    align-items: center;
                    padding-top: 5px;



                    .label {
                        font-weight: bold;
                        width: 150px;
                        text-align: center;
                        padding-right: 8px;
                    }

                    .grades {
                        display: flex;
                        align-items: center;
                        padding: 0;

                        .grade {
                            background-size: 100% 100%;
                            width: 26px;
                            height: 30px;
                            margin-right: 5px;
                        }
                    }
                }

                .rowDivide {
                    padding-top: 30px;
                }
            }
        }
    }
}