
        @import "src/styles/scss/common.scss";
        
.pageHeaderTabs{
  position: absolute;
  width: 100%;
  top:40px;
  background-color: $white;
  z-index:1000;
  :global(.adm-tabs-tab-active) {
    color: $primary_color;
  
    box-sizing: border-box;
  
  }:global(.adm-tabs-tab-line) {
    background-color: $primary_color;
  
    box-sizing: border-box;
  
  }
}
.form{
  :global(.adm-form-item-label){
    font-family: PingFang SC;
    font-size: 14px;
    font-weight: 500;
    line-height: 20px;
    letter-spacing: 0em;
    text-align: left;

  }
  input[type=number],
  :global(.i-formcurrent__text){
    color: $primary_color !important;
  }
}
.qrCode{
  text-align: center;
}
.tip{
  margin-top: 20px;
  text-align: center;
  font-size: 12px;
  color: #888;
  line-height: 20px;
  padding-bottom: 10px;
  .mainNet{
    color: $primary_color !important;
  }
}
.paymentType{
  margin-bottom: 16px;
  text-align: center;
  line-height: 20px;
  font-size: 14px;
    color: $primary_color !important;
}
.footer{
  position: absolute;
  bottom: 0;
  width: 100%;
  background-color: $white;
  box-shadow: 0 0 10px rgba(0, 0, 0, .1);
}
.main{
  padding: 40px 10px 60px;
}