
        @import "src/styles/scss/common.scss";
        
.form {
    padding: {
        top: 24px;
        left: 40px;
        right: 40px;
    }


}

.input {
    margin-bottom: 24px;

    &:nth-child(3) {
        margin-bottom: 0;
    }
}

.icon {
    margin-left: 10px;
}

.dx_token{
    height: auto !important;
}


.imageCode {
    width: 92px;
    height: 34px;
}

.options {
    margin-top: 10px;

    display: flex;
    align-items: center;
    justify-content: space-between;

    font-style: normal;
    font-weight: 400;
    font-size: 12px;
    line-height: 17px;

    color: $font_color_sub;
}

.forgotPassword {
    font-style: normal;
    font-weight: 400;
    font-size: 12px;
    line-height: 17px;

    color: $font_color_sub;
    padding-right: 0;
}

.submitButton {
    margin-top: 8px !important;

    width: 295px;
    height: 44px;
    /* 渐变色1 */

    background: linear-gradient(93.54deg, $submit_btn_bg_from 2.8%, $primary_color 94.95%);
    border-radius: 60px;

    font-style: normal;
    font-weight: 500;
    font-size: 16px;
    line-height: 22px;

    color: $white;

}
.switchLoginBox{
    margin-top: 18px;
    display: flex;
    justify-content: flex-end;
}

.switchLoginMode {
    display: block;
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 20px;
    /* identical to box height */
    /* 主题色 */
    color: $primary_color;
}

.logo {
    display: flex;
    width: 220px;
    height: 72px;
    justify-content: center;
    align-items: center;
    gap: 2.628px;
    flex-shrink: 0;

    background-image: url("~@/assets/images/logo.png");
    background-size: 100%;
    background-repeat: no-repeat;
    margin: 20px auto;
}

.submitButtonUnbind {
    display: flex;
    width: 347px;
    height: 44px;
    justify-content: center;
    align-items: center;
    gap: 10px;
    flex-shrink: 0;
    margin: 62px auto;

    border-radius: 8px;
    background: var(--1, linear-gradient(137deg, $submit_btn_bg_from 0%, $primary_color 100%));

    color: $white;
    text-align: center;
    font-family: PingFang SC;
    font-size: 16px;
    font-style: normal;
    font-weight: 500;
    line-height: normal;
}

.unbindFieldWrapper {
    padding: 0px 14px;

    :global(.i-input .i-icon) {
        display: none;
    }
}

.unbindFieldTopLabel {
    display: flex;
    align-items: center;
}

.unbindFieldLabel {
    color: var(--1, $font_color);
    font-family: PingFang SC;
    font-size: 16px;
    font-style: normal;
    font-weight: 500;
    line-height: normal;

    margin-left: 8px;
}