
        @import "src/styles/scss/common.scss";
        
.i-layout {
    height: 100%;
    display: flex;
    flex-direction: column;

    &--bg {
        background-size: 100%;
        background-repeat: no-repeat;
        background-image: url("~@/assets/images/mask-bg.png");
    }

    &__main {
        flex: 1;

        &--scrollable {
            overflow-x: hidden;
            overflow-y: auto;
            scrollbar-width: none;
            -ms-overflow-style: none;

            &::-webkit-scrollbar {
                display: none;
            }
        }
    }
}