
        @import "src/styles/scss/common.scss";
        
.header{
  width: 347px;
  height: 128px;
  background-color: $white;
  border-radius: 12px;
  margin: 10px auto;
  display: flex;
  font-size: 14px;
  box-shadow: 0px 4px 4px 0px rgba(204, 223, 206, 0.25);

  &_avatar{
    flex:1;
    justify-content: center;
    align-items: center;
    display: flex;

   
  }
  &_content{
    flex:3
  }
  :global(.adm-image){
    height: 56px;
    width: 56px;
  }
}
.btn{
  background: linear-gradient(93.54deg, $submit_btn_bg_from 2.8%, $primary_color 94.95%);
  color: $white;
  font-size: 12px !important;
  border-radius: 4px;
  width: 36px;
  height: 24px;
  padding: 3px;
}
.loading{
  width: 20px;
  color: $primary_color;
  font-size: 13px;
}
.amount {
  color: $primary_color;
  flex: 1;
  text-align: right;
  display: inline-block;
  font-weight: 600;
  margin-right: 6px;
  font-family: DINPro;
  font-size: 14px;
  font-weight: 400;
  line-height: 18px;
  letter-spacing: 0em;
  text-align: right;

  :global(.adm-space-item){
    line-height: 24px;
  }
}
.header_item{
  font-size: 13px;
  margin: 12px 4px;
  display: flex;
  align-items: center;
  .header_item_title{
    font-size: 12px;
    margin-left: 6px;
  }
}
.actions{
  display: flex;
  .action_btn{
    background: linear-gradient(93.54deg, $submit_btn_bg_from 2.8%, $primary_color 94.95%);
    color: $white;
    font-size: 12px;
    border-radius: 4px;
    border:none;
    flex: 1;
    margin: 12px;
    padding: 10px;

  }
}
.header_avatar_image{
  padding: 12px;
  position: relative;
  .header_user_level{
    margin-top: 20px;
    font-family: PingFang SC;
    font-size: 12px;
    font-weight: 400;
    
    border-radius: 4px;
    line-height: 20px;
    color: $white;
    text-align: center;
    background: linear-gradient(90.84deg, #FF7D45 0.98%, #FF5654 100%);

  }
  .avatar_box{
    top: 4px;
    left: 0;
    width: 80px;
    position: absolute;
    height: 80px;
    &.default{
      background-repeat: no-repeat;
      background-size: 100%;
      background-image: url("~@/assets/avatar/avatar_default.png");
    }
    &.silver{
      background-repeat: no-repeat;
      background-size: 100%;
      background-image: url("~@/assets/avatar/avatar_silver.png");
    }
    &.normal{
      background-repeat: no-repeat;
      background-size: 100%;
      background-image: url("~@/assets/avatar/avatar_normal.png");
    }
    &.diamond{
      background-repeat: no-repeat;
      background-size: 100%;
      background-image: url("~@/assets/avatar/avatar_diamond.png");
    }
    &.platinum{
      background-repeat: no-repeat;
      background-size: 100%;
      background-image: url("~@/assets/avatar/avatar_platinum.png");
    }
    &.crown{
      background-repeat: no-repeat;
      background-size: 100%;
      background-image: url("~@/assets/avatar/avatar_crown.png");
    }
  }
  
}

.listTable {
  :global(.adm-grid){
    height: 100%;
    align-items: center;
    align-content: center;
    justify-content: center;
    justify-items: center;
  }
  :global(.adm-grid-item) {
    font-size: 12px;
    text-align: center;
    
  }

  :global(.adm-list-body) {
    border: none;
  }

  :global(.adm-list-item-content-main) {
    padding: 0;
  }

  :global(.adm-list-item-content) {

    padding: 0 0;
    // border: none;
  }

  .list_row {
    height: 40px;
    line-height: 1.25;
    border-bottom: $border_opacity;
  }

  &.listTableHeader {
    .listTableHeaderItem {
      padding: 0 0;
      height: 32px;
      line-height: 32px;
      background-color: #e6f8ee;
      box-sizing: border-box;
    }

    :global(.adm-grid-item) {
      font-size: 10px;
      text-align: center;
    }
  }
}

.mt{
  margin-top: 14px;
}


.profession_box{
  position: relative;
  display: flex;
  flex-flow: column;
  height: 156px;
  margin: 0px 10px;
  padding: 0px 8px;
  background-repeat: no-repeat;
  background-size: 100% 100%;
  background-image: url("~@/assets/images/profession/bg.png");
  .line{
    position: absolute;
    top: 26px;
    left: -8px;
    width: calc(100% + 8px);
    z-index: 2;
  }
  .p_title{
    height: 26px;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  .revenue{
    height: 11px;
  }
  .go_profession{
    height: 12px;
    cursor: pointer;
  }
  .p_cont{
    flex: 1;
    display: flex;
    align-items: center;
    justify-content: space-around;
    text-align: center;
  }

  
  .img_text1{
    width: 172px;
  }
  .img_text2{
    width: 145px;
    margin-top: 5px;
  }

  .img_wrap{
    width: 80%;
    padding: 0px 2px;
    display: flex;
    align-items: center;
    flex-flow: column;
    cursor: pointer;
    .img_user{
      width: 100%;
    }
    .img_btn{
      position: absolute;
      bottom: 12px;
      height: 27px;
    }
  }


}



