
        @import "src/styles/scss/common.scss";
        
.list {
  border: none;
  background-color: transparent;

  .listItemContent {
    display: flex;
    align-items: center;
    cursor: pointer;

    .listItemExtra {
      display: flex;
      align-items: baseline;
    }

    .listItemExtraText {
      color: #AAAAAA;
      font-size: 14px;
      margin-right: 10px;
    }

    .listItemExtraIcon {
      margin: 0 6px
    }
  }

  .listItemText {
    font-size: 14px;
    flex: 1;
  }



  :global(.adm-list-body) {
    border: none;
    margin-top: 0;
    background-color: transparent
  }

  :global(.adm-list-item) {
    background-color: transparent
  }


  :global(.adm-list-item-content) {
    border: none;
    padding: 0;
    align-items: center;
  }

  :global(.adm-list-item-content-main) {
    border-bottom: 1px solid rgba(43, 162, 69, .1);
    box-sizing: border-box;
  }
}