
        @import "src/styles/scss/common.scss";
        
.buy-trade-board-bg {
    &.WaitingSellerConfirm {
        background: linear-gradient(145deg, #00B0A6 0%, #00C667 100%);
    }

    &.SellerCancelsPurchase {
        background: linear-gradient(145deg, #FF7B69 0%, #FFAC30 100%);
    }

    &.WaitingBuyerPay {
        background: linear-gradient(145deg, #00B0A6 0%, #00C667 100%);
    }

    &.BuyerNotPay {
        background: linear-gradient(145deg, #FF7B69 0%, #FFAC30 100%);
    }

    &.BuyerPaymentExceedsTimeLimit {
        background: linear-gradient(145deg, #FF7B69 0%, #FFAC30 100%);
    }

    &.BuyerConfirmsOrderHasBeenPaid {
        background: linear-gradient(145deg, #00B0A6 0%, #00C667 100%);
    }

    &.SellerHasNotReceivedPayment {
        background: linear-gradient(133deg, #FF7D45 0%, #FF5654 100%);
    }

    &.BuyerAppeals {
        background: linear-gradient(133deg, #CBCBCB 0%, #8C8C8C 100%);
    }

    &.AdminCancelsOrder {
        background: linear-gradient(133deg, #FFC672 0%, #E4A400 100%);
    }

    &.AdminCompletesOrder {
        background: linear-gradient(145deg, #05C057 0%, #00E865 100%);
    }

    &.SellerCompletesOrder {
        background: linear-gradient(145deg, #05C057 0%, #00E865 100%);
    }

    &.BuyerNotAppeals {
        background: linear-gradient(133deg, #FF7D45 0%, #FF5654 100%);
    }
}

.buy-trade-board-status {

    background-size: contain;
    background-repeat: no-repeat;

    &.WaitingSellerConfirm {
        width: 162px;
        height: 120px;
        background-image: url('~@/assets/images/trade/WaitingSellerConfirm.png');
    }

    &.SellerCancelsPurchase {
        width: 86px;
        height: 96px;

        margin: {
            top: 18px;
            left: 33px;
            right: 33px;
        }

        background-image: url('~@/assets/images/trade/SellerCancelsPurchase.png');
    }

    &.WaitingBuyerPay {
        width: 162px;
        height: 120px;
        background-image: url('~@/assets/images/trade/WaitingSellerConfirm.png');
    }

    &.BuyerNotPay {
        width: 86px;
        height: 96px;

        margin: {
            top: 18px;
            left: 33px;
            right: 33px;
        }

        background-image: url('~@/assets/images/trade/SellerCancelsPurchase.png');
    }

    &.BuyerPaymentExceedsTimeLimit {
        width: 86px;
        height: 96px;

        margin: {
            top: 18px;
            left: 33px;
            right: 33px;
        }

        background-image: url('~@/assets/images/trade/SellerCancelsPurchase.png');
    }

    &.BuyerConfirmsOrderHasBeenPaid {
        width: 118px;
        height: 118px;

        margin: {
            top: 7px;
            left: 8px;
            right: 14px;
        }

        background-image: url('~@/assets/images/trade/BuyerConfirmsOrderHasBeenPaid.png');
    }

    &.SellerHasNotReceivedPayment {
        width: 103px;
        height: 103px;

        margin: {
            left: 9px;
            right: 6px;
        }

        background-image: url('~@/assets/images/trade/SellerHasNotReceivedPayment.png');
    }

    &.BuyerAppeals {
        width: 118px;
        height: 118px;

        margin: {
            left: 22px;
            right: 14px;
        }

        background-image: url('~@/assets/images/trade/BuyerAppeals.png');
    }

    &.AdminCancelsOrder {
        width: 118px;
        height: 118px;

        margin: {
            left: 22px;
            right: 14px;
        }

        background-image: url('~@/assets/images/trade/BuyerAppeals.png');
    }

    &.AdminCompletesOrder {
        width: 118px;
        height: 114px;

        margin: {
            top: 8px;
            left: 23px;
            right: 11px;
        }

        background-image: url('~@/assets/images/trade/AdminCompletesOrder.png');
    }

    &.SellerCompletesOrder {
        width: 118px;
        height: 114px;

        margin: {
            top: 8px;
            left: 23px;
            right: 11px;
        }

        background-image: url('~@/assets/images/trade/AdminCompletesOrder.png');
    }

    &.BuyerNotAppeals {
        width: 103px;
        height: 103px;

        margin: {
            left: 9px;
            right: 6px;
        }

        background-image: url('~@/assets/images/trade/SellerHasNotReceivedPayment.png');
    }
}