
        @import "src/styles/scss/common.scss";
        
.page {
  height: 100%;
  overflow-y: auto;
  background-repeat: no-repeat;
  background-size: 100%;
  background-image: url("~@/assets/images/mask-bg.png");


  .bg {
    position: relative;
    background-repeat: no-repeat;
    background-size: 100%;
    background-image: url("~@/assets/images/orders/poster.png");
    height: 120px;
    margin: 10px;
    font-size: 16px;
    display: flex;
    align-items: center;
    padding: 10px 16px;

    color: $white;

    .text {
      flex: 1;
      line-height: 24px;
    }

    .title {
      font-weight: 500;
    }

    .bg_img {
      margin-top: 20px;
      height: 100px;
      width: 160px;
      background-repeat: no-repeat;
      background-size: 100%;
      background-image: url("~@/assets/images/orders/frame_bg.png");
    }
  }

  .orderList {
    background-color: $sub_page_bg_color;



    :global(.adm-list-body) {
      border: none;
      margin-top: 0;
      background-color: transparent
    }

    :global(.adm-list-item) {
      background-color: transparent;
      padding: 0;
    }


    :global(.adm-list-item-content) {
      border: none;
      padding: 0;
      align-items: center;
    }

    :global(.adm-list-item-content-main) {
      border-bottom: none;
      box-sizing: border-box;
      padding: 0;
    }
  }

  .tabs {
    :global(.adm-tabs-tab) {
      font-size: 16px;
      height: 32px;
      line-height: 22px;
      box-sizing: border-box;
      padding: 0;
      padding: 0 30px;

    }

    :global(.adm-tabs-tab-active) {
      color: $primary_color;

      box-sizing: border-box;

    }

    :global(.adm-tabs-tab-line) {
      background: $primary_color;

    }

    :global(.adm-tabs-header) {
      border: none
    }
  }

  // .content {
  //   flex: 1;
  //   overflow-y: auto;
  // }

  .footer {
    height: 80px
  }
}

.filter {
  padding-top: 10px;
  position: sticky;
  top: 0;
  background-color: $white;
  // width: 100%;

  z-index: 1;
  
}
.filterContent{
  display: flex;
  padding: 0 10px;
  align-items: center;
  :global(.adm-button ){
    font-size: 12px;
  }
}
.filterTitle{
  font-size: 12px;
  color: #888888;
}

.whitespace {
  height: 120px;
}

