
        @import "src/styles/scss/common.scss";
        
.profession_detail{
  padding: 10px;
  // background: #FBE0C1;
  background-image: url("~@/assets/images/merchant/subsidy/a_bg.png");
  background-size: 100% 100%;
  .earn_page_detail_table_header{
    margin: 10px;
  }
}


.earn_page{
  padding-bottom: 50px;
}
.earn_page_title{
  font-family: PingFang SC;
  font-size: 16px;
  font-weight: 600;
  line-height: 22px;
  letter-spacing: 0em;
  text-align: center;
  margin-top: 10px;
}

.earn_page_title2{
  font-family: PingFang SC;
  font-size: 14px;
  font-weight: 400;
  line-height: 20px;
  letter-spacing: 0em;
  text-align: center;
  margin-top: 10px;
  span{
      color: $primary_color
  }
}
.earn_page_block{
  padding:10px;
  line-height: 1.5;
  .earn_page_title{
      font-family: PingFang SC;
      font-size: 16px;
      font-weight: 600;
      line-height: 22px;
      letter-spacing: 0em;
      text-align: left;
  }
}
.earn_page_steps{
  padding: 10px 0 ;
  display: flex;
  flex-wrap: wrap;
  .earn_page_step{
      font-family: PingFang SC;
      font-size: 13px;
      font-weight: 600;
      line-height: 20px;
      letter-spacing: 0em;
      text-align: center;
      width: 50%;
      height: 60px;
      color: $white;

      padding: 3px 2px;
      &_content{
          border-radius: 10px;
          height: 100%;
          display: flex;
          flex-direction: column;
          align-items: center;
          justify-content: center;
          text-align: center;
          &.green{
              background: linear-gradient(100.64deg, #05C057 7.25%, #00E865 81.94%);
          }
          &.red{
              background: linear-gradient(90.84deg, #FF7D45 0.98%, #FF5654 100%);
          }
      }
      &_text{
          font-size: 12px;
      }

  
      
  }
  
}

.earn_page_detail_block{
  line-height: 1.5;
  .earn_page_detail_title{
      font-family: PingFang SC;
      font-size: 16px;
      font-weight: 600;
      line-height: 22px;
      letter-spacing: 0em;
      text-align: left;
  }
  .earn_page_detail_content{
      font-size: 14px;
      p{
          margin-top: 10px;
      }
  }
  .earn_page_detail_label{
    font-weight: 600;
  }
 
}
.earn_page_detail_table{
  border-color:$primary_color;
  width: 100%;
  background:  rgba(104, 255, 170, 0.16);
  border-bottom-left-radius: 8px;
  border-bottom-right-radius: 8px;
  overflow: hidden;
  thead{
    background: #4ce417;
  }
  
  th{
      font-family: PingFang SC;
      font-size: 14px;
      font-weight: 600;
      line-height: 20px;
      letter-spacing: 0em;
      text-align: center;
      border-right: 1px solid #0f6303;
      border-bottom: 1px solid #0f6303;
      vertical-align: middle;
      padding: 8px 4px;
      &:last-of-type{
          border-right: none;
      }
  }
  td{
      font-family: PingFang SC;
      font-size: 12px;
      font-weight: 400;
      line-height: 20px;
      letter-spacing: 0em;
      text-align: center;
      vertical-align: center;

      border-right: 1px solid #0f6303;
      border-bottom: 1px solid #0f6303;
      vertical-align: middle;
      &:last-of-type{
          border-right: none;
      }
      padding: 8px 4px;
      span{
          font-size: 12px;
          color: $font_color_sub;
      }
  }
  tr:last-of-type{
      td{
          border-bottom: none;
      }
      
  }
  tbody{
      tr{
        background: #FCF0DF;
      }
  }
  
}
.earn_page_detail_table_header{
  font-family: PingFang SC;

  font-family: PingFang SC;
  font-size: 16px;
  font-weight: 600;
  line-height: 22px;
  letter-spacing: 0em;
  text-align: center;
  span{
      font-weight: 400;
  }
}
.earn_page_agreement{
  margin-top: 10px;
  font-family: PingFang SC;
  font-size: 14px;
  font-weight: 400;
  line-height: 20px;
  letter-spacing: 0em;
  text-align: justified;
  display: flex;

  &_text{
      margin-left: 10px;
  }
  &_rule{
      font-weight: 600;
      color: $primary_color;
  }
}

.earn_bg_box{
  position: relative;
  margin-top: 12px;
  height: 230px;
  text-align: center;
  background-image: url("~@/assets/images/merchant/subsidy/bg.png");
  background-size: 85%;
  background-repeat: no-repeat;
  background-position: center;
  .earn_logo{
    position: absolute;
    top: 5px;
    left: 50%;
    right: 50%;
    height: 46px;
    transform: translate(-50%);
  }
  .earn_txt{
    position: absolute;
    bottom: 25px;
    height: 30px;
    line-height: 30px;
    background-image: url("~@/assets/images/merchant/subsidy/txt_bg1.png");
    background-size: 70%;
    background-repeat: no-repeat;
    background-position: center;
    img{
      width: 60%;
    }
  }
  .earn_txt2{
    position: absolute;
    bottom: 0;
    width: 100%;
    height: 22px;
    // background-image: url("~@/assets/images/merchant/subsidy/txt_bg2.png");
    background-size: 100%;
    background-repeat: no-repeat;
    text-align: center;
    img{
      height: 14px;
    }
  }
}

.headLine {
  // background:  transparent url("data:image/svg+xml;base64,");
  background: transparent url("data:image/svg+xml;base64,");
    
  .leftBottom {
    display: block;
    font-size: 13px;
    font-weight: 600;
    color: #000;
    float: left;
    margin-left: 5px;
    margin-top: 15px;
  }
  .rightTop {
    display: block;
    font-size: 13px;
    font-weight: 600;
    color: #000;
    float: right;
    margin-right: 5px;
  }
}
