
        @import "src/styles/scss/common.scss";
        
.form {
  .tip {
    font-size: 14px;
    color: $font_color_tip;
  }

  :global(.adm-form-item.adm-form-item-vertical .adm-form-item-label) {
    font-size: 16px;
    font-weight: 500;
    line-height: 22px;
    padding: $page_padding;
    color: $font_color;
  }

  :global(.adm-input-element) {
    font-size: 14px;
    line-height: 20px;
    padding: 0 10px;


    padding: $page_padding;
    background-color: $white;
    color: $primary_color;

    &::placeholder {
      color: $font_color_tip;
    }
  }

  :global(.adm-text-area-element) {
    font-size: 14px;
    line-height: 20px;
    padding: $page_padding;
    background-color: $white;

    &::placeholder {
      color: $font_color_tip;
    }
  }

  :global(.adm-list-body) {
    background-color: transparent;
    border-top: none;
    border-bottom: none;
  }

  :global(.adm-list-item) {
    background-color: transparent;
    padding: 0;
  }


  :global(.adm-list-item-content) {
    border: none;
    padding: 0;
    align-items: center;
  }

  :global(.adm-list-item-content-main) {
    border-bottom: none;
    box-sizing: border-box;
    padding: 0;
  }

  .submit {
    background: linear-gradient(93.54deg, $submit_btn_bg_from 2.8%, $primary_color 94.95%);
    width: 95%;
    display: block;
    margin: 20px auto;
    color: $white;
    font-size: 14px;
    line-height: 26px;
  }

  .camera {
    display: flex;

    justify-content: center;

    .camera_content {
      width: 48px;
      height: 48px;
      background-color: $primary_color;
      border-radius: 48px;
      text-align: center;
      display: flex;
      align-items: center;
    }
  }
}