
        @import "src/styles/scss/common.scss";
        
.qr {
    width: 136.514px;
    height: 136.512px;
    background: url("~@/assets/svg/qr.svg"), $primary_color;
    margin: 40px auto 12px;
    border: 11.74px solid $primary_color;
    border-radius: 8px;
}

.qrImage {
    width: 136.514px;
    height: 136.512px;
    margin: 40px auto 12px;
    border-radius: 8px;
}

.tip {
    color: var(--2, $font_color_sub);
    text-align: center;
    font-family: PingFang SC;
    font-size: 16px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
    text-align: center;
}

.confirm {
    display: flex;
    width: 347px;
    height: 44px;
    justify-content: center;
    align-items: center;
    gap: 10px;
    flex-shrink: 0;

    border-radius: 8px;
    background: var(--1, linear-gradient(137deg, $submit_btn_bg_from 0%, $primary_color 100%));

    color: $white;
    text-align: center;
    font-family: PingFang SC;
    font-size: 16px;
    font-style: normal;
    font-weight: 500;
    line-height: normal;

    margin: 210px auto;
}