
        @import "src/styles/scss/common.scss";
        
.i-level {
    display: flex;

    &__icon {
        width: 18px;
        height: 20px;
        margin-left: 5px;
        background-size: 100% 100%;
        background-repeat: no-repeat;

        &:first-child {
            margin-left: 0;
        }

        &--1 {
            background-image: url("~@/assets/icons/grades/g1@3x.png");

            &.i-level__icon {
                &--up {
                    background-image: url("~@/assets/icons/grades/g1l@3x.png");
                }
            }
        }

        &--2 {
            background-image: url("~@/assets/icons/grades/g2@3x.png");

            &.i-level__icon {
                &--up {
                    background-image: url("~@/assets/icons/grades/g2l@3x.png");
                }
            }
        }

        &--3 {
            background-image: url("~@/assets/icons/grades/g3@3x.png");

            &.i-level__icon {
                &--up {
                    background-image: url("~@/assets/icons/grades/g3l@3x.png");
                }
            }
        }

        &--4 {
            background-image: url("~@/assets/icons/grades/g4@3x.png");

            &.i-level__icon {
                &--up {
                    background-image: url("~@/assets/icons/grades/g4l@3x.png");
                }
            }
        }

        &--5 {
            background-image: url("~@/assets/icons/grades/g5@3x.png");

            &.i-level__icon {
                &--up {
                    background-image: url("~@/assets/icons/grades/g5l@3x.png");
                }
            }
        }

    }
}