
        @import "src/styles/scss/common.scss";
        
.adm-center-popup-wrap {
    min-width: 320px;
    max-width: 320px;
}


.adm-dialog-body {
    padding-top: 0 !important;
}

.adm-dialog-content {
    padding: 0;


}

.adm-dialog-footer .adm-dialog-action-row>.adm-dialog-button {
    border-right: none;
}

.i-dialog {
    background: $white;
    border-radius: 16px;
    width: 320px;
    overflow: hidden;

    &__body {
        min-height: 125px
    }

    &__title {
        height: 22px;

        font-family: 'PingFang SC';
        font-style: normal;
        font-weight: 500;
        font-size: 16px;
        line-height: 22px;
        text-align: center;

        /* 主题色 */

        color: $primary_color;

        padding: {
            top: 30px;
            bottom: 34px;
        }
    }

    &__content {

        font-family: 'PingFang SC';
        font-style: normal;
        font-weight: 400;
        font-size: 14px;
        line-height: 20px;

        /* 文本色1 */

        color: $font_color;

        padding : {
            left: 10px;
            right: 10px;
            bottom: 14px;
        }
    }

    &__footer {
        display: flex;
        border-top: 1px solid #eee;
        overflow: hidden;

        .i-dialog {
            &__button {
                flex: 1;
                border: none;
                border-right: 1px solid var(--border-color);

                &:last-child {
                    border: none;
                }
            }
        }
    }

    &__button {
        font-family: 'PingFang SC';
        font-style: normal;
        font-weight: 500;
        font-size: 16px;
        line-height: 22px;
        text-align: center;

        &--confirm {
            /* 主题色 */

            color: $primary_color;
        }

        &--cancel {
            /* 文本色2 */

            color: $font_color_sub;
        }
    }


    &--warning {


        .i-dialog {
            &__title {
                padding: {
                    top: 27px;
                }
            }

            &__icon {


                &--warning {

                    display: block;
                    width: 40px;
                    height: 40px;
                    margin: 34px auto 0;
                    // 
                    color: $primary_color;
                }
            }
        }

    }
}