
        @import "src/styles/scss/common.scss";
        
.body {
    border-top-left-radius: 6px;
    border-top-right-radius: 6px;
}

.title {
    box-sizing: border-box;
    height: 46px;
    font-family: PingFang SC;
    font-size: 16px;
    font-weight: 500;
    line-height: 22px;
    letter-spacing: 0em;

    padding: {
        top: 12px;
        left: 14px;
    }

    border-bottom: 1px solid rgba($primary_color, 0.15);
}

.content {
    padding: 0 14px;
}

.fieldTitle {
    color: $font_color;
    font-size: 16px;
    font-weight: 500;

    padding: {
        top: 12px;
        bottom: 8px;
    }
}

.filed {
    font-size: 18px;
    font-weight: 700;
}

.fieldClear {
    color: #ACACAC;
    text-align: right;
    font-size: 14px;
}

.selector {
    :global(.adm-selector-item) {
        border-radius: 4px;
        border: 1px solid rgba($primary_color, 0.1);
        background-color: $white;

        color: $font_color_sub;
        text-align: center;
        font-size: 14px;
    }

    :global(.adm-selector-item-active),
    :global(.adm-selector-item-multiple-active) {
        background: rgba($primary_color, 0.2);
        border-color: rgba($primary_color, 0.2);
    }
}

.buttonGroup {
    display: flex;
    justify-content: space-between;
    padding-top: 20px;
}

.resetButton {
    box-sizing: border-box;
    display: flex;
    align-items: center;
    justify-content: center;

    width: 168px;
    height: 36px;
    border: 1px solid $primary_color;
    border-radius: 4px;

    color: $primary_color;
    text-align: center;
    font-size: 16px;
    font-weight: 500;
}

.applyButton {
    box-sizing: border-box;
    display: flex;
    align-items: center;
    justify-content: center;

    width: 168px;
    height: 36px;
    border-radius: 4px;
    background: var(--1, linear-gradient(137deg, $submit_btn_bg_from 0%, $primary_color 100%));

    color: $white;
    text-align: center;
    font-size: 16px;
    font-weight: 500;
}

.bottomPrompt {
    color: #ACACAC;
    text-align: center;
    font-size: 12px;
    padding: 16px 0;
}

.typeBox{
    display: flex;
    align-items: center;
}

.icon{
    display: inline-block;
    width: 16px;
    height: 16px;
    margin-right: 4px;
    background-size: 100%;
}

.bank {
    background-image: url("~@/assets/icons/payments/bank.png")
}

.alipay {
    background-image: url("~@/assets/icons/payments/alipay.png")
}

.wechat {
    background-image: url("~@/assets/icons/payments/wechat.png")
}

.digital_rmb {
    background-image: url("~@/assets/icons/payments/digital_rmb.png")
}