
        @import "src/styles/scss/common.scss";
        
.content {
    position: relative;
    display: flex;
}

.board {
    padding-bottom: 55px;
}

.boardBgTitle {
    color: $white;
    font-family: PingFang SC;
    font-size: 16px;
    font-style: normal;
    font-weight: 500;
    line-height: normal;

    margin-top: 16px;
}

.boardBgDesc {
    color: $white;
    font-family: PingFang SC;
    font-size: 12px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;

    margin-top: 4px;
    white-space: pre-wrap;
}

.boardBgDescHighlight {
    color: red;
    font-weight: bold;
}

.boardContent {
    padding-top: 46px;
    padding-bottom: 16px;
}

.boardContentCountdown {
    color: var(--1, $font_color);
    text-align: center;
    font-family: PingFang SC;
    font-size: 14px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
}

.boardContentOrderNumber {
    color: var(--3, #888);
    text-align: center;
    font-family: PingFang SC;
    font-size: 12px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;

    display: flex;
    align-items: center;
    justify-content: center;

    margin-top: 8px;
}

.blockTitle {
    display: flex;
    align-items: center;
}
.spanColor{
    color: $primary_color;
}

.vcoinNumber {
    color: var(--unnamed, $primary_color);
    leading-trim: both;
    text-edge: cap;
    font-family: DINPro;
    font-size: 24px;
    font-style: normal;
    font-weight: 700;
    line-height: normal;

    padding-left: 17px;
}

.vcoin {
    margin-left: 8px;
}

.payments {
    padding-left: 12px;
}

.paidScreenShot {
    display: block;
    width: 347px;
    height: 126px;
    flex-shrink: 0;

    border-radius: 8px;
    border: 1px solid var(--unnamed, $primary_color);

    margin: 0 auto;
    text-align: center;
    vertical-align: middle;
    line-height: 126px;
}

.uploader {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;

    width: 347px;
    height: 126px;
    flex-shrink: 0;

    border-radius: 8px;
    border: 1px dashed var(--unnamed, $primary_color);

    margin: 0 auto 15px;

    position: relative;
}

.uploaderDelete {
    position: absolute;
    top: 0;
    right: 0;
    background-color: rgba(100, 100, 100, 0.281);
    color: white;
}

.plsUploadPaidScreenShot {
    color: var(--1, $font_color);
    text-align: justify;
    font-family: PingFang SC;
    font-size: 14px;
    font-style: normal;
    font-weight: 400;
    line-height: 24px;
    /* 171.429% */
    margin-top: 8px;
}

.uploadInput {
    position: absolute;
    width: 347px;
    height: 126px;
    opacity: 0;
    :global(.adm-image-uploader-upload-button-wrap){
        position: absolute;
        width: 100%;
        height: 100%;
    }
}

.tradingNotice {
    color: var(--7, #F76260);
    text-align: justify;
    font-family: PingFang SC;
    font-size: 14px;
    font-style: normal;
    font-weight: 400;
    line-height: 24px;
    /* 171.429% */

    padding: 0 14px;
}

.actions {
    display: flex;
    align-items: center;
    justify-content: space-around;

    padding: 0 14px;
    padding-top: 10px;
    padding-bottom: 18px;
    margin-bottom: 40px;
}
.floatBtn{
    width: 90%;
    display: flex;
    flex-wrap: wrap;
    position: fixed;
    bottom: 20px;
    border-radius: 8px;
    box-shadow: 0px 0px 12px 4px #eee;
    .downSlide{
        width: 100%;
        text-align: center;
        font-size: 12px;
        margin-bottom: 5px;
        color: $primary_color;
    }
}
.TransferBtn{
    width: 90%;
    display: flex;
    flex-wrap: wrap;
    position: fixed;
    bottom: 14px;
    border-radius: 8px;
    backdrop-filter: blur(3px);
    .downSlide{
        width: 100%;
        display: flex;
        align-items: center;
        justify-content: center;
        font-size: 12px;
        margin-bottom: 5px;
        color: $primary_color;
        .iconArrow{
            transform: rotate(90deg);
        }
    }
}

.actionCancelTrading {
    flex: 1;
    display: flex;
    width: 170px;
    height: 40px;
    justify-content: center;
    align-items: center;
    gap: 10px;
    flex-shrink: 0;

    color: $white;
    text-align: center;
    font-family: PingFang SC;
    font-size: 16px;
    font-style: normal;
    font-weight: 500;
    line-height: normal;

    border-radius: 8px;
    background: var(--2, linear-gradient(133deg, #FF7D45 0%, #FF5654 100%));
}

.actionHasBeenTransfered {
    flex: 1;
    margin-left: 15px;
    display: flex;
    width: 170px;
    height: 40px;
    justify-content: center;
    align-items: center;
    gap: 10px;
    flex-shrink: 0;

    color: $white;
    text-align: center;
    font-family: PingFang SC;
    font-size: 16px;
    font-style: normal;
    font-weight: 500;
    line-height: normal;

    border-radius: 8px;
    background: var(--1, linear-gradient(137deg, $submit_btn_bg_from 0%, $primary_color 100%));
}

.applyArbitrateDialog {
    background-color: transparent;
}


.autoOrder{
    position: relative;
}

.centerBtn {
    display: flex;
    justify-content: center;
    padding-top: 5px;
    .goPayBtn{
        margin: 6px 0px;
        display: flex;
        width: 120px;
        height: 40px;
        justify-content: center;
        align-items: center;
        gap: 10px;
        flex-shrink: 0;

        color: $white;
        text-align: center;
        font-family: PingFang SC;
        font-size: 15px;
        font-style: normal;
        font-weight: 500;
        line-height: normal;

        border-radius: 8px;
        background: var(--1, linear-gradient(137deg, $submit_btn_bg_from 0%, $primary_color 100%));
    }
}

.description {
    font-size: 12px;
    color: #1f1f1f;
    padding: 10px 0;
    text-align: center;
}

.certificated {
    width: 111px;
    height: 24px;
    background-image: url("~@/assets/images/certificated.png");
    background-size: 100%;
}

.certification {
    position: absolute;
    top: 80px;
    right: 15px;
    width: 76px;
    height: 46px;
    background-image: url("~@/assets/images/certification.png");
    background-size: 100%;
    background-repeat: no-repeat;
    transform: rotate(16deg);
}