
        @import "src/styles/scss/common.scss";
        
.userInfo {
  .userInfoContent {
    display: flex;
    width: 100%;
    justify-content: center;
  }

  .userInfoAvatar {
    height: 64px;
    width: 64px;
    border-radius: 64px;
    background-color: $white;
    position: relative;
    cursor: pointer;


    &.defaultAvatar {
      background-repeat: no-repeat;
      background-size: 100%;
      background-image: url("~@/assets/images/avatar.png");
    }

    .userInfoAvatarAction {
      position: absolute;
      bottom: 0;
      right: 0;
    }
  }


  .userInfoName {
    font-size: 20px;
    font-weight: 700;

    line-height: 23px;
  }

  .userInfoRank {
    color: $font_color;
    font-family: PingFang SC;
    font-size: 14px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
  }

  .userInfoActions {
    margin: 14px;
    box-sizing: border-box;
    background-color: $white;
    box-shadow: 0px 4px 4px 0px rgba(204, 223, 206, 0.25);
    padding: 20px 10px;
    box-sizing: border-box;
    border-radius: 16px;
    color: $font_color_sub;

    .userInfoAction {
      text-align: center;
      font-size: 14px;
      line-height: 22px;
      cursor: pointer;


      &:first-of-type {

        border-right: 1px solid $primary_color;
      }

      &:last-of-type {
        border-left: 1px solid $primary_color;
      }
    }

    .userInfoActionIconText {
      margin-top: 10px;
    }
  }

  .userInfoCode {
    margin: 20px;
    text-align: center;
    font-size: 14px;
    color: #AAA;
  }
}