
        @import "src/styles/scss/common.scss";
        
.balance_info {
  height: 70px;

  text-align: center;
  display: flex;
  flex-direction: column;
  justify-content: space-around;

  .balance_info_code {
    font-size: 12px;
    color: #AAAAAA;
  }

  .balance_info_vol {
    color: $primary_color;
    font-size: 32px;
    font-weight: 800;
  }

  .balance_info_icon {
    background-position: bottom;
  }
}

.loading{
  width: 20px;
  color: $primary_color;
  font-size: 18px;
  margin-left: 8px;
  height:20px;
  padding: 0;
  &_icon{
    height: 12px;
    width: 12px;
    padding-left:4px;
  }
}