
        @import "src/styles/scss/common.scss";
        
.page{
  position: relative;
  
}

.form{
  :global(.adm-form-item-label){
    font-family: PingFang SC;
    font-size: 14px;
    font-weight: 500;
    line-height: 20px;
    letter-spacing: 0em;
    text-align: left;

  }
  input[type=number],
  :global(.i-formcurrent__text){
    color: $primary_color !important;
  }
}
.goHelp{
  text-align: right;
  padding: 10px;
  a{
    font-size: 12px;
    color: #1677ff;
  }
  
}
.tip{
  text-align: center;
  font-size: 12px;
  color: #888;
  line-height: 20px;
  padding: 10px 0 ;
  margin-top: 10px;
  .mainNet{
    color: $primary_color !important;
  }
}
.paymentType{
  margin-bottom: 16px;
  text-align: center;
  line-height: 20px;
  font-size: 14px;
    color: $primary_color !important;
}
.header{
  display: flex;
  padding:0 10px;
  font-size: 12px;
  margin-bottom: 10px;
}
.header_extra,
.header_title{
  font-size: 12px;
  font-weight: 600;
}
.qrCode{
  text-align: center;
}

.FooterTips{
  font-size: 12px;
  padding: 10px;
  line-height: 20px;
  .tip_header{
    font-weight: 600;
  }
}
.footer{
  position: absolute;
  bottom: 0;
  width: 100%;
  background-color: $white;
  box-shadow: 0 0 10px rgba(0, 0, 0, .1);
}
.main{
  padding: 0px 10px 60px;
}
