
        @import "src/styles/scss/common.scss";
        
.popup {
  :global(.adm-popup-body-position-bottom) {
    border-radius: 16px 16px 0 0;
    overflow: hidden;
  }

  .content {
    height: 330px;
    padding: 12px;
    box-sizing: border-box;

    :global(.adm-number-keyboard-popup) {
      display: block !important;

      :global(.adm-popup-body) {
        transform: translate(0, 0) !important;
        background-color: $primary_color;
        padding: 8px;
        box-sizing: border-box;
        color: $white;
      }

      :global(.adm-number-keyboard-main) {
        justify-content: space-between;
      }

      :global(.adm-number-keyboard-key) {
        // flex: 1 1 30%;
        width: 100px;
        flex: auto;
        border: none;
        margin: 6px;
        background-color: transparent;
      }

      :global(.adm-number-keyboard-key-number) {
        border: none;
        background-color: rgba(241, 241, 241, .2);
        border-radius: 12px;
      }



    }

    :global(.adm-passcode-input-cell-container) {
      width: 100%;

      justify-content: space-between;


    }

    :global(.adm-passcode-input-seperated) {
      width: 100%;
    }

    :global(.adm-passcode-input-cell) {
      width: 50px;
      height: 50px;
      border-color: $primary_color;
    }

    :global(.adm-passcode-input-cell-focused) {
      color: $primary_color;
      border-color: $primary_color;
      box-shadow: 0 0 2px 0 $primary_color;

      &::after {
        background-color: $primary_color;
      }
    }
  }
}


.input_items {
  display: flex;
  font-size: 32px;
  color: $font_color;
  justify-content: space-around;
  padding: 16px 0;


  .input_item {
    text-align: center;
    line-height: 50px;
    width: 50px;
    background-repeat: no-repeat;
    background-size: 100%;
    background-image: url("~@/assets/images/common/square.png");

  }
}


.input_content {
  width: 100%;
  bottom: 0;
  left: 0;

  .input_pane {
    background-color: $primary_color;
    color: #F1F1F1;
    font-size: 24px;
    font-weight: 500;
    height: 240px;
    display: flex;
    justify-content: space-around;
    flex-direction: column;
    padding: 10px 0;
    box-sizing: border-box;
    border-radius: 12px 12px 0 0;
  }

  .input_pane_row {
    display: flex;
    justify-content: space-around;
    flex-direction: row;
    text-align: center;
    padding: 0 0.1rem;

    .input_pane_col {
      line-height: 45px;
      // width: 100px;
      flex: 1;
      margin: 6px;

      border-radius: 12px;

      &.num {
        background-color: rgba(241, 241, 241, 0.2);

        &:hover {
          background-color: rgba(241, 241, 241, .5);
        }
      }
    }
  }
}