
        @import "src/styles/scss/common.scss";
        
.step {
  line-height: 20px;

  .stepTitle {
    font-size: 20px;
    line-height: 20px;
    display: flex;
    margin: 10px 0;

    .stepTitleStatus {
      color: $primary_color;
    }

    .stepTitleOrder {
      color: $primary_color;
      border-right: 2px solid $primary_color;
      padding-right: 10px;
    }

    .stepTitleText {
      padding-left: 10px;
    }
  }

  .stepDesc {

    color: #888;
    font-size: 14px;
  }

  .stepImg{
    width: 90%;
  }

  .stepIcon {
    background-color: $primary_color;
    border-radius: 20px;
  }
}