
        @import "src/styles/scss/common.scss";
        
.content {
    position: relative;
    display: flex;
}

.board {
    padding-bottom: 55px;
}

.boardBgTitle {
    color: $white;
    font-family: PingFang SC;
    font-size: 16px;
    font-style: normal;
    font-weight: 500;
    line-height: normal;

    margin-top: 16px;
}

.boardBgDesc {
    color: $white;
    font-family: PingFang SC;
    font-size: 12px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;

    margin-top: 4px;
    white-space: pre-wrap;
}

.boardBgDescHighlight {
    color: red;
    font-weight: bold;
}

.boardContent {
    padding-top: 46px;
    padding-bottom: 16px;
}

.boardContentCountdown {
    color: var(--1, $font_color);
    text-align: center;
    font-family: PingFang SC;
    font-size: 14px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
}

.boardContentOrderNumber {
    color: var(--3, #888);
    text-align: center;
    font-family: PingFang SC;
    font-size: 12px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;

    display: flex;
    align-items: center;
    justify-content: center;

    margin-top: 8px;
}

.blockTitle {
    display: flex;
    align-items: center;
}
.spanColor{
    color: $primary_color;
}
.vcoinNumber {
    color: var(--unnamed, $primary_color);
    leading-trim: both;
    text-edge: cap;
    font-family: DINPro;
    font-size: 24px;
    font-style: normal;
    font-weight: 700;
    line-height: normal;

    padding-left: 17px;
}

.vcoin {
    margin-left: 8px;
}

.payments {
    padding-left: 12px;
}

// 买家资讯
.lineOfSellerInfo {
    margin-top: 8px;
    display: flex;
    justify-content: space-between;
}

.itemOfSellerInfo {
    flex: 1;
    display: flex;

    font-size: 12px;
    font-family: PingFang SC;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
}

.keyOfSellerInfo {
    color: #888;
}

.valueOfSellerInfo {
    color: $primary_color;
}

// 系统买家
.systemBuyerCertification {
    padding: 16px 0;
}

.systemBuyerInfo {
    display: flex;
    align-items: center;
}

.systemBuyerAvatar {
    width: 40px;
    height: 40px;
    flex-shrink: 0;

    border-radius: 20px;
}

.systemBuyerTag {
    color: $black;
    text-align: justify;
    font-family: PingFang SC;
    font-size: 14px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;

    margin-left: 23px;
}

.systemBuyerTagValue {
    margin-left: 5px;
}

.systemBuyerDesc {
    color: var(--2, $font_color_sub);
    text-align: justify;
    font-family: PingFang SC;
    font-size: 12px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;

    margin-top: 16px;
}

.tradingNotice {
    color: var(--7, #F76260);
    text-align: justify;
    font-family: PingFang SC;
    font-size: 14px;
    font-style: normal;
    font-weight: 400;
    line-height: 24px;
    /* 171.429% */

    padding: 0 14px;
}

// 订单操作按钮
.actions {
    display: flex;
    gap: 15px;
    align-items: center;
    justify-content: space-around;

    padding: 0 14px;
    padding-top: 10px;
    padding-bottom: 18px;
    margin-bottom: 40px;
    color: $white;
    text-align: center;
    font-family: PingFang SC;
    font-size: 16px;
    font-style: normal;
    font-weight: 500;
    line-height: normal;
}

.floatBtn{
    width: 90%;
    display: flex;
    position: fixed;
    bottom: 20px;
    border-radius: 8px;
    box-shadow: 0px 0px 12px 4px #eee
}
.TransferBtn{
    width: 90%;
    display: flex;
    flex-wrap: wrap;
    position: fixed;
    bottom: 10px;
    border-radius: 8px;
    backdrop-filter: blur(3px);
    .downSlide{
        width: 100%;
        display: flex;
        align-items: center;
        justify-content: center;
        font-size: 12px;
        margin-bottom: 5px;
        color: $primary_color;
        .iconArrow{
            transform: rotate(90deg);
        }
    }
}


.actionNegative {
    flex: 1;
    height: 40px;

    color: $white;
    text-align: center;
    font-family: PingFang SC;
    font-size: 16px;
    font-style: normal;
    font-weight: 500;
    line-height: normal;

    border-radius: 8px;
    background: var(--2, linear-gradient(133deg, #FF7D45 0%, #FF5654 100%));
}

.actionPositive {
    flex: 1;
    height: 40px;

    color: $white;
    text-align: center;
    font-family: PingFang SC;
    font-size: 16px;
    font-style: normal;
    font-weight: 500;
    line-height: normal;

    border-radius: 8px;
    background: var(--1, linear-gradient(137deg, $submit_btn_bg_from 0%, $primary_color 100%));
}

.riskNotice {
    color: var(--3, #888);
    font-family: PingFang SC;
    font-size: 12px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
    padding: 14px;
}

.riskNoticeTitle {
    color: var(--3, #888);
    font-family: PingFang SC;
    font-size: 14px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
    text-align: center;
    padding-bottom: 15px;
}