
        @import "src/styles/scss/common.scss";
        
.content {
  width: 350px;
  margin: 0 auto;
  height: 130px;
  box-sizing: border-box;
  background-size: 100%;
  background-repeat: no-repeat;

  font-size: 12px;
  color: $primary_color;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

  .icon {
    height: 32px;
    width: 32px;
    margin-bottom: 10px;
    background-size: 100%;
    background-repeat: no-repeat;
    background-image: url("~@/assets/images/payment/icon-jia-green.png");
  }
}

.imageUploader {
  :global(.adm-space-item) {
    width: 100%;
    height: 100%;

  }

  :global(.adm-image-uploader-cell) {
    height: 130px;
    width: 100%;
    text-align: center;
  }

  :global(.adm-image) {
    width: 100%;
    height: 100%;

    img {
      object-fit: contain;
    }

    // margin: 0 auto;
  }
}