
        @import "src/styles/scss/common.scss";
        
.i-input {

    &__content {
        border: none;
        outline: none;
    }

    &--default {
        box-sizing: border-box;

        height: 44px;

        overflow: hidden;
        display: flex;
        align-items: center;

        border-bottom: 1px solid rgba($primary_color, 0.1);

        // 暂未实现默认样式
        .i-input {

            &__content {
                flex: 1;
                min-width: 0;
                height: 44px;
                border: none;
                outline: none;

                padding: {
                    left: 10px;
                    right: 10px;
                }

                font-style: normal;
                font-weight: 400;
                font-size: 14px;
                line-height: 20px;

                color: $font_color;

                &::placeholder {
                    color: var(--4, #AAA);
                    text-align: justify;
                    font-family: PingFang SC;
                    font-size: 14px;
                    font-style: normal;
                    font-weight: 400;
                    line-height: 24px;
                    /* 171.429% */
                }
            }
        }
    }

    // 登陆注册页面样式
    &--auth {
        box-sizing: border-box;

        width: 295px;
        height: 44px;

        background: rgba(255, 255, 255, 0.5);
        box-shadow: 0px 6px 20px rgba(164, 213, 165, 0.23), inset -0.5px -0.5px 0px rgba(1, 177, 78, 0.2), inset 0.5px 0.5px 0px #DEFFEC;
        border-radius: 40px;
        overflow: hidden;
        display: flex;
        align-items: center;

        .i-input {
            &__content {
                flex: 1;
                min-width: 0;
                height: 44px;
                border: none;
                outline: none;

                padding: {
                    left: 10px;
                    right: 10px;
                }

                font-style: normal;
                font-weight: 400;
                font-size: 12px;
                line-height: 20px;

                color: $font_color;

                &::placeholder {
                    font-style: normal;
                    font-weight: 400;
                    font-size: 12px;
                    line-height: 17px;

                    color: #AAAAAA;
                }
            }
        }

    }

    // trade 页面筛选弹窗中输入框样式
    &--plain {
        display: flex;
        align-items: center;
        height: 36px;
        border-radius: 4px;
        border: 1px solid $primary_color;
        overflow: hidden;
        box-sizing: border-box;

        .i-input {
            &__content {
                flex: 1;
                height: inherit;
                border: none;

                color: $primary_color;
                font-size: inherit;
                font-weight: inherit;
                padding-left: 8px;
                padding-right: 8px;

                &::placeholder {
                    color: #ACACAC;
                    font-size: 14px;
                    font-family: PingFang SC;
                    font-style: normal;
                    font-weight: 400;
                    line-height: normal;
                }
            }
        }
    }

    &--textarea {
        position: relative;
        height: max-content;
        display: flex;
        align-items: center;
        border-radius: 4px;
        border: 1px solid $primary_color;
        overflow: hidden;
        box-sizing: border-box;

        .i-input {
            &__content {
                flex: 1;
                min-height: 80px;
                border: none;

                font-size: inherit;
                font-weight: inherit;
                padding: 8px;

                scrollbar-width: 0;

                &::-webkit-scrollbar {
                    width: 0;
                    background-color: transparent;
                }

                &::placeholder {
                    color: #ACACAC;
                    font-size: 14px;
                    font-family: PingFang SC;
                    font-style: normal;
                    font-weight: 400;
                    line-height: normal;
                }
            }

            &__word-limit {
                position: absolute;
                right: 4px;
                bottom: 2px;
                font-size: 12px;
                color: #646566;
                line-height: 16px;
                text-align: right;
            }
        }
    }

    .i-icon {

        &.psw-show,
        &.psw-hide {
            height: 16px;
            width: 16px;
        }
    }

}