
        @import "src/styles/scss/common.scss";
        
.i-trade-board {
    position: relative;
    padding: 16px 22px 70px;

    &__bg {
        position: relative;
        border-radius: 8px;
        width: 331px;
        height: 120px;
    }

    &__mask {
        position: absolute;
        width: 375px;
        background-repeat: no-repeat;
        background-size: 100% auto;
        background-image: url("~@/assets/svg/trade-board-mask.svg");
        left: 0;
        bottom: 0;
    }

    &__content {
        position: relative;
    }
}