
        @import "src/styles/scss/common.scss";
        
.form {
  .tip {
    font-size: 14px;
    color: $font_color_tip;
  }

  :global(.adm-form-item.adm-form-item-vertical .adm-form-item-label) {
    font-size: 16px;
    font-weight: 500;
    line-height: 22px;
    padding: $page_padding;
    color: $font_color;
  }

  :global(.adm-input-element) {
    font-size: 14px;
    line-height: 20px;
    padding: 0 10px;


    padding: $page_padding;
    background-color: $white;
    color: $primary_color;

    &::placeholder {
      color: $font_color_tip;
    }
  }

  :global(.adm-text-area-element) {
    font-size: 14px;
    line-height: 20px;
    padding: $page_padding;
    background-color: $white;

    &::placeholder {
      color: $font_color_tip;
    }
  }

  :global(.adm-list-body) {
    background-color: transparent;
    border-top: none;
    border-bottom: none;
  }

  :global(.adm-list-item) {
    background-color: transparent;
    padding: 0;
  }


  :global(.adm-list-item-content) {
    border: none;
    padding: 0;
    align-items: center;
  }

  :global(.adm-list-item-content-main) {
    border-bottom: none;
    box-sizing: border-box;
    padding: 0;
  }





}

.submit {
  background: linear-gradient(93.54deg, $submit_btn_bg_from 2.8%, $primary_color 94.95%);
  width: 95%;
  display: block;
  margin: 20px auto;
  color: $white;
  font-size: 14px;
  line-height: 26px;
}

.wrapper {
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: stretch;

  .content {
    flex: 1;
    overflow-y: auto;
  }

  .approval {
    text-align: center;
    flex: 1;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
  }






}