
        @import "src/styles/scss/common.scss";
        
.page {
  display: flex;
  flex-direction: column;
  align-items: stretch;
  height: 100%;
  overflow: hidden;

  .content {
    flex: 1;
    overflow-y: auto;
  }

  .header {
    display: flex;
    justify-content: space-around;
    font-size: 16px;
    line-height: 22px;
    text-align: center;
    background-color: $white;
    border-bottom: $border_opacity;
    padding: $page_padding;

    .headerItem {
      flex: 1
    }
  }
}


.orderList {
  background-color: $sub_page_bg_color;



  :global(.adm-list-body) {
    border: none;
    margin-top: 0;
    background-color: transparent
  }

  :global(.adm-list-item) {
    background-color: transparent;
    padding: 0;
  }


  :global(.adm-list-item-content) {
    border: none;
    padding: 0;
    align-items: center;
  }

  :global(.adm-list-item-content-main) {
    border-bottom: none;
    box-sizing: border-box;
    padding: 0;
  }
}


.listItem {
  display: flex;
  padding: $page_padding;
  box-sizing: border-box;
  border-bottom: $border_opacity;
  justify-content: space-around;
  align-items: center;

  .listItemDate {
    font-size: 14px;
    font-weight: 400;
    line-height: 18px;
    text-align: center;
    flex: 1;
  }

  .listItemType {
    font-size: 14px;
    font-weight: 400;
    line-height: 18px;
    text-align: center;
    flex: 1;
  }

  .listItemAmount {
    font-size: 16px;
    font-weight: 400;
    line-height: 20px;
    color: $primary_color;
    text-align: center;
    flex: 1;
  }
}

.filter {
  position: sticky;
  top: 0;
  background-color: $white;
  width: 100%;
  // z-index: 1;
}