
        @import "src/styles/scss/common.scss";
        
.id_card {
    // margin: 20px 0;
    display: flex;
    align-items: center;
    justify-content: center;
}

.id_card_front {
    width: 260px;
    height: 166px;
    background-repeat: no-repeat;
    background-size: 100%;
    background-image: url("~@/assets/images/common/id_card_f.png");
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

.id_card_back {
    width: 260px;
    height: 166px;
    background-repeat: no-repeat;
    background-size: 100%;
    background-image: url("~@/assets/images/common/id_card_b.png");
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

.id_card_icon {
    height: 56px;
    width: 56px;
    background-color: $primary_color;
    border-radius: 56px;
    text-align: center;
    box-shadow: 0 0.4px 12px rgba(77, 193, 95, .8);
    display: flex;
    justify-content: center;
    align-items: center;
}

.id_card_text {
    font-size: 14px;
    font-weight: 500;
    line-height: 20px;
    text-align: center;
    color: $primary_color;
    margin-top: 10px;

}

.id_card_img {
    width: 260px;
    height: 166px;
    margin: 10px auto;
}