
        @import "src/styles/scss/common.scss";
        
.scan_face {
    .scan_face_title {
        text-align: center;
        font-size: 16px;
        font-weight: 500;
        line-height: 22px;
        margin: 20px auto;
    }

    .scan_face_content {
        display: flex;
        justify-content: center;
        align-items: center;
    }

    .scan_face_bg {
        width: 200px;
        height: 170px;

        background-repeat: no-repeat;
        background-size: 100%;
        background-image: url("~@/assets/images/common/scan-face.png");
    }

    .scan_face_tip {
        padding: $page_padding;
        font-size: 14px;
        line-height: 24px;
        color: $font_color_sub;

        &_text {
            color: $primary_color;
        }
    }
}

.countDown {
    display: flex;
    justify-content: center;
    font-size: 13px;
    align-items: center;
}


.scan_face_steps {
    display: flex;
    margin: 30px auto;
    justify-content: space-around;

    .scan_face_step_icon {
        width: 56px;
        height: 56px;
    }

    .scan_face_step_text {
        color: $font_color_sub;
        font-size: 14px;
        line-height: 24px;
        margin-top: 10px;
    }

    .scan_face_1 {
        .scan_face_step_icon {
            background-repeat: no-repeat;
            background-size: 100%;
            background-image: url("~@/assets/images/common/scan-face-1.png");
        }

    }

    .scan_face_2 {
        .scan_face_step_icon {
            background-repeat: no-repeat;
            background-size: 100%;
            background-image: url("~@/assets/images/common/scan-face-2.png");
        }
    }

    .scan_face_3 {
        .scan_face_step_icon {
            background-repeat: no-repeat;
            background-size: 100%;
            background-image: url("~@/assets/images/common/scan-face-3.png");
        }
    }
}

.fileSelect {
    height: 100%;
    width: 100%;
    opacity: 0;
    cursor: pointer;
}