
        @import "src/styles/scss/common.scss";
        
.page {
  display: flex;
  flex-direction: column;
  align-items: stretch;
  height: 100%;
  overflow: hidden;

  .content {
    flex: 1;
    overflow-y: auto;

    .title {
      font-size: 14px;
      line-height: 20px;
      color: $font_color;

    }

    .date {
      font-size: 12px;
      line-height: 24px;
      color: #888;
    }

  }


}

.item {
  display: flex;
  cursor: pointer;
  padding: 10px;
  border-bottom: 1px solid rgba(43, 162, 69, .1);
  box-sizing: border-box;

  .text {
    margin: 0 10px;
    flex: 1
  }
}

.header {
  display: flex;
  justify-content: space-around;
  font-size: 16px;
  line-height: 22px;
  text-align: center;
  background-color: $white;
  border-bottom: $border_opacity;
  padding: $page_padding;

  .headerItem {
    flex: 1
  }
}

.filter {
  position: sticky;
  top: 0;
  background-color: $white;
  // z-index: 1;
}