
        @import "src/styles/scss/common.scss";
        
.page {
  display: flex;
  flex-direction: column;
  align-items: stretch;
  height: 100%;
  background-repeat: no-repeat;
  background-size: 100%;
  background-image: url("~@/assets/icons/home/mask_group.png");
  overflow-y: auto;

  .page_header {
    height: 70px;
  }

  // .page_content {
  //   flex: 1;
  //   overflow-y: auto;
  // }

  .page_footer {
    border-top: $border_opacity;
  }

  &.bg {
    background-repeat: no-repeat;
    background-size: 100%;
    background-image: url("~@/assets/images/mask-bg.png");

    .page_content {
      background-color: transparent;
    }

    .page_header {
      border-bottom: none;
    }
  }

}