
        @import "src/styles/scss/common.scss";
        

.form{
  :global(.adm-form-item-label){
    font-family: PingFang SC;
    font-size: 14px;
    font-weight: 500;
    line-height: 20px;
    letter-spacing: 0em;
    text-align: left;

  }
  input[type=number],
  :global(.i-formcurrent__text){
    color: $primary_color !important;
  }
  :global(.i-formcopyicon__text){
    text-align: right;
    padding-right: 3px;
  }
}

.main{
  padding: 0px 10px 60px;
  position: relative;
  .loading_box{
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    background-color: $white;
    z-index: 9;
  }
  .loading_icon{
      margin: 50px auto;
  }
}
